<template>
    <transition name="bounce">
        <div class="ui-dialog_page" v-show="show" @keydown.esc="handleClosePage">
            <div class="ui-head">
                <span>查看美工图片</span>
                <span class="el-icon-close" @click="handleClosePage"></span>
            </div>
            <div class="ui-left_nav">
                <ul>
                    <li
                        v-for="(item, index) in operationList"
                        :key="index"
                        :class="{ 'ui-active': activePage === index && 'ui-active' || submitSkuList.indexOf(item.label) > -1}"
                        @click="handleToggleEditArea(index, item)">
                        <span>{{ item.label }}</span>
                    </li>
                </ul>
            </div>
            <div class="ui-content_area" :style="{height: contentHeight}" v-if="listInfo[activeVal]">
                <div v-show="!row.checks">
                    <span class="ui-label">图片类型</span>
                    <el-select v-model="imgType" size="small" disabled>
                        <el-option
                            v-for="(label, value) in imgTypeList"
                            :key="value"
                            :label="label"
                            :value="value"></el-option>
                    </el-select>
                </div>
                <div class="ui-content_area--title" v-show="row.checks">
                    <span>实拍图商品有LOGO</span>
                </div>
                <div class="ui-img--area">
                    <span class="ui-img--area--title">主图</span>
                    <ul class="ui-img--area--item clearFix">
                        <li v-for="(item, index) in handleUpImgInfo('main_pics', '1')"
                            :class="item.is_sign && 'ui-is_border'">
                            <img :src="item.url" alt="">
                            <div class="ui-img--mask">
                                <span class="el-icon-delete" v-if="ope"
                                      @click="handleDeleteImage(index, 'main_pics')"></span>
                                <span class="el-icon-zoom-in"
                                      @click="handleLookImage(item.url, index)"></span>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="ui-img--area">
                    <span class="ui-img--area--title">辅图</span>
                    <ul class="ui-img--area--item clearFix">
                        <li v-for="(item, index) in handleUpImgInfo('assiant_pics', '1')"
                            :class="item.is_sign && 'ui-is_border'">
                            <img :src="item.url" alt="">
                            <div class="ui-img--mask">
                                <span class="el-icon-delete" v-if="ope"
                                      @click="handleDeleteImage(index, 'assiant_pics')"></span>
                                <span class="el-icon-zoom-in"
                                      @click="handleLookImage(item.url, index)"></span>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="ui-content_area--title" v-show="row.checks">
                    <span>实拍图商品无LOGO</span>
                </div>
                <div class="ui-img--area" v-show="row.checks">
                    <span class="ui-img--area--title">主图</span>
                    <ul class="ui-img--area--item clearFix">
                        <li v-for="(item, index) in handleUpImgInfo('main_pics', '2')"
                            :class="item.is_sign && 'ui-is_border'">
                            <img :src="item.url" alt="">
                            <div class="ui-img--mask">
                                <span class="el-icon-delete" v-if="ope"
                                      @click="handleDeleteImage(index, 'main_pics')"></span>
                                <span class="el-icon-zoom-in"
                                      @click="handleLookImage(item.url, index)"></span>
                            </div>
                        </li>
                    </ul>
                </div>
                <div class="ui-img--area" v-show="row.checks">
                    <span class="ui-img--area--title">辅图</span>
                    <ul class="ui-img--area--item clearFix">
                        <li v-for="(item, index) in handleUpImgInfo('assiant_pics', '2')"
                            :class="item.is_sign && 'ui-is_border'">
                            <img :src="item.url" alt="">
                            <div class="ui-img--mask">
                                <span class="el-icon-delete" v-if="ope"
                                      @click="handleDeleteImage(index, 'assiant_pics')"></span>
                                <span class="el-icon-zoom-in"
                                      @click="handleLookImage(item.url, index)"></span>
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="ui-ope--area" v-if="ope">
                <el-button type="warning" size="small" @click="handleSaveOrSubmit(1)">保存</el-button>
                <el-button type="primary" size="small" v-if="!activePage" @click="handleSaveOrSubmit(2)">提交</el-button>
            </div>
            <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        </div>
    </transition>
</template>

<script>
import {
    previewSaveInfo,
    previewSubmitInfo
} from '@/api/design_department/artist_manage/art_picture_upload';
import CONFIG from '@/assets/js/config';

export default {
    name: 'PreviewImage',
    props: {
        show: false,
        row: {
            type: Object,
            default () {
                return {};
            }
        },
        ope: false
    },
    data () {
        return {
            activeVal: '',
            activePage: -1,
            submitSkuList: [],
            editPageData: {},
            imgTypeList: {},
            imgType: '',
            listInfo: {},

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            contentHeight: CONFIG.setTableHeight('ui-dialog_page', 80, true)
        };
    },
    computed: {
        operationList () {
            let skuList = [];
            let spu = '';
            try {
                this.imgTypeList = this.row.imgTypeList;
                this.imgType = this.row.imgType;
                Object.keys(this.row.sku).forEach(value => {
                    skuList.push({
                        value: value,
                        label: value
                    });
                });
                spu = Object.keys(this.row.spu)[0];

                this.listInfo = {
                    ...this.row.spu,
                    ...this.row.sku
                };
                console.log(this.listInfo);
            } catch (e) {
                console.log(e);
                console.log(this.row);
            }
            return [
                {
                    label: 'SPU',
                    value: spu
                },
                ...skuList
            ];
        },
    },
    methods: {
        handleClosePage () {
            this.$emit('update:show', false);
        },

        // 切换编辑区域
        handleToggleEditArea (index, item) {
            this.activePage = index;
            this.activeVal = item.value;
        },

        // 预览图片
        handleLookImage (src, index) {
            this.isShowLookImage = true;
            // let imgList = src.map(value => {
            //     return value.url;
            // });
            // imgList.push(index);
            this.lookImageUrl = src;
        },

        // 删除图片
        handleDeleteImage (index, key) {
            this.listInfo[this.activeVal][key].splice(index, 1);
        },

        // 保存
        handleSaveOrSubmit (type) {
            let {assiant_pics, main_pics} = this.listInfo[this.activeVal];
            let assiantPics = assiant_pics && assiant_pics.map(value => {
                return value.url;
            });

            let mainPics = main_pics && main_pics.map(value => {
                return value.url;
            });

            let valueObj = {
                [this.activeVal]: {
                    assiant_pics: assiantPics,
                    main_pics: mainPics
                }
            };

            let obj = {
                image_list: JSON.stringify({
                    [!this.activePage ? 'spu' : 'sku']: valueObj
                }),
                spu: this.operationList[0].value,
                image_type: this.row.imgType
            };

            if (type === 1) {
                previewSaveInfo(obj).then(({data}) => {
                    this.handleOperationCallBack(data);
                });
            } else {
                previewSubmitInfo(obj).then(({data}) => {
                    this.handleOperationCallBack(data);
                    this.$parent.handleQueryInfoCallBack(data);
                    this.handleClosePage();
                });
            }
        },

        handleOperationCallBack (data) {
            if (data.status) {
                this.$message.success('操作成功！');
            } else {
                this.$message.error(data.errorMess || '操作失败！');
            }
        },

        // 渲染图片数据
        handleUpImgInfo(key, type) {
            if (this.row.checks) {
                return this.listInfo[this.activeVal][type][key];
            } else {
                return this.listInfo[this.activeVal][key];
            }
        }
    },
    watch: {
        show (val) {
            if (val) {
                this.handleToggleEditArea(0, {value: Object.keys(this.row.spu)[0]});
            } else {
                this.activePage = -1;
            }
        }
    }
};
</script>

<style scoped lang="less">

@border-color: #e6e6e6;
@active-color: #5b8eff;
.ui-dialog_page {
    width: 100%;
    height: 100%;
    background: #FFFFFF;
    top: 0;
    left: 0;
    position: absolute;
    z-index: 9;
    text-align: left;

    .ui-head {
        background: #f3f3f3;
        height: 45px;
        line-height: 45px;
        padding: 0 20px;
        font-size: 16px;
        position: relative;
        z-index: 2;

        .el-icon-close {
            float: right;
            font-size: 22px;
            cursor: pointer;
            margin-top: 10px;
        }
    }

    .ui-left_nav {
        width: 140px;
        height: 100%;
        position: absolute;
        left: 0;
        border-right: 1px solid @border-color;
        border-top: 1px solid @border-color;
        z-index: 1;

        ul > li {
            height: 40px;
            border-bottom: 1px solid #e6e6e6;
            line-height: 40px;
            font-size: 13px;
            padding-left: 10px;
            cursor: pointer;
            font-weight: bold;

            &.ui-active {
                background: @active-color;
                color: #FFFFFF;
            }
        }
    }

    .ui-left_nav {
        top: 0;
        padding-top: 45px;
        box-sizing: border-box;
    }

    /deep/ .el-checkbox {
        margin-right: 20px;
        margin-left: 0;
    }

    .ui-content_area {
        overflow: auto;
        padding-top: 20px;
        margin-left: 160px;

        .ui-label {
            font-size: 14px;
            color: #666666;
        }

        .ui-content_area--title {
            margin-top: 40px;
            span {
                color: #666666;
                font-size: 18px;
            }
        }

        .ui-img--area--title {
            border-left: 2px solid #333333;
            padding-left: 5px;
            display: block;
            font-size: 14px;
            line-height: 14px;
            margin-top: 30px;
        }

        .ui-img--area--item {
            li {
                border: 1px solid transparent;
                width: 130px;
                height: 130px;
                float: left;
                margin-right: 20px;
                margin-top: 10px;
                position: relative;

                img {
                    width: 100%;
                    height: 100%;
                }

                &:hover .ui-img--mask {
                    display: block;
                }
            }
        }

        .ui-img--mask {
            display: none;
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background-color: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 130px;

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }
    }
}

/deep/ .el-collapse {
    border: 0 none;
    overflow: hidden;
}

/deep/ .el-collapse-item {
    .el-collapse-item__header {
        background: #f3f3f3;
        height: 30px;
        line-height: 30px;
        text-align: left;
        padding-left: 10px;
        color: #5a8dfd;

        .el-collapse-item__arrow {
            line-height: 30px;
        }
    }

    .el-collapse-item__wrap {
        border: 0 none;
    }

    .el-collapse-item__content {
        padding: 10px;
        box-sizing: border-box;
    }
}

.bounce-enter-active {
    animation: bounce-in .5s;
}

.bounce-leave-active {
    animation: bounce-in .5s reverse;
}

@keyframes bounce-in {
    0% {
        transform: scale(0) translate(-1000px, -1000px) rotateX(360deg);
    }
    100% {
        transform: scale(1) translate(0, 0) rotateX(0deg);
    }
}

.ui-ope--area {
    margin-top: 100px;
    text-align: center;
}

.ui-is_border {
    border-color: #51D902!important;
}
</style>



