import CopyEditPage from './CopyEditPage';
import {
    autoAddTableInx,
    getDefaultTime,
    getNumByTime,
    getArrListByObj,
} from '@/services/index';

import { mapActions, mapGetters } from 'vuex';
import {
    assignSpu,      // 指派人员
    reAssignSpu,    // 重新指派人员
    getRejectInfo,   //获得驳回下拉
    upLoadPhoto,    //驳回图片上传
    rejectSubmit    //驳回
} from '@/api/copy_department/copy_edit_module/copy_edit_list';
import { getUserByfunction } from '@/api/common';

export default {
    data () {
        return {
            // 指派类别
            assignType: 1, // 1 指派， 2 重新指派；

            //高级筛选
            filterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            // 搜索缩进
            showSearch: true,

            queryData: {
                moduleType: '1', //1 待分配  2 待编辑  3 已编辑
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
            },

            optionList: {
                productList: [],
                functionInfoList: {},
                workUserList: {},
                statusList: {},
                moduleTypeList: [
                    {
                        label: '待分配',
                        value: '1'
                    },
                    {
                        label: '待编辑',
                        value: '2'
                    }
                ],
                workerList: {},
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
                operateUserList: [],
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0
            },

            // 编辑
            isEditShow: false,
            formLabelWidth: '110px',
            saveCheckId: '',

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 上传列表
            saveFileList: [],

            //文案小组和商品分类
            classSearch: [
                {
                    title: '全部',
                    number: 400
                },
                {
                    title: '手机周边',
                    number: 35
                },
                {
                    title: '全部',
                    number: 350
                },
                {
                    title: '服饰珠宝',
                    number: 50000
                },
                {
                    title: '服饰珠宝',
                    number: 5
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 50121212
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501213213
                }
            ],

            //指派任务弹窗
            isShowAssign: false,

            //驳回
            isShowReject: false,
            rejectData: [],
            rejectList: [],
            rejectDefault: {},

            isShowEdit: false,
            editSpu: '',
            languageList: {}
        };
    },
    components: {
        CopyEditPage
    },
    computed: {
        ...mapGetters(['queryParamsInfo', 'copyEditListInfo'])
    },
    methods: {
        ...mapActions(['GetCopyEditListInfo']),
        autoAddTableInx,

        // 操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.operateUseMax = 30;
                    break;
                case 'hours':
                    this.queryData.operateUseMax = 24;
                    break;
                case 'minutes':
                    this.queryData.operateUseMax = 60;
                    break;
            }
        },
        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.functionInfo}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },

        //字符串 转换 数组
        getArrByStr (str) {
            let arr = str + '';
            arr = arr.split(',');
            return arr;
        },

        handleClickTest () {
            this.isShowEdit = true;
        },
        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['copy_edit_list']);
            }

            let obj = {
                spu: this.queryData.productSPU, //SPU
                spu_name: this.queryData.productName, //SPU名称
                supplier_code: this.queryData.supplierCode, //供应商代码
                supplier_name: this.queryData.supplierName, //供应商名称
                category_id: this.queryData.productList, //分类
                function: this.queryData.functionInfo, //职能
                audit_status: this.queryData.auditState, //审核状态
                operator: this.queryData.workUser, //操作人员
                operation_use_time: '', //操作用时
                operation_time_start: this.queryData.operateTime[0], //操作开始
                operation_time_end: this.queryData.operateTime[1], //操作结束
                query_type: this.queryData.moduleType, //1 待分配  2 待编辑  3 已编辑
                opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
            };

            this.GetCopyEditListInfo(obj)
                .then(({data}) => {
                    this.handleQueryInfoCallBack(data);
                })
                .catch(err => {
                    console.log(err);
                });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                moduleType: this.queryData.moduleType,
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo(
                {
                    limit: val,
                    offset: 1
                },
                true
            );
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo(
                {
                    limit: this.pageData.currentList,
                    offset: val
                },
                true
            );
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
            this.handleQueryInfo();
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo(
                {
                    isExcel: 1
                },
                true
            );
        },

        // 点击编辑
        handleClickEdit (row) {
            this.isShowEdit = true;
            this.editSpu = row.spu[0];
        },

        // 关闭编辑
        handleCloseEdit () {
            for (let key in this.editData) {
                if (Array.isArray(this.editData[key])) {
                    this.editData[key] = [];
                } else {
                    this.editData[key] = '';
                }
            }
            this.$refs.editData.resetFields();
        },

        // 格式化用戶列表數據
        formatMemberUser (originSource) {
            let targetSource = {};
            for (let key in originSource) {
                targetSource = {
                    ...targetSource,
                    [key]: originSource[key]['user_name']
                };
            }
            return targetSource;
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'copy_edit_list',
                value: JSON.stringify(this.queryData)
            });

            if ('select_list' in data) {
                // 渲染下拉
                this.optionList.productList = [...data.select_list.category_list];
                this.optionList.functionInfoList = getArrListByObj(data.select_list.function, true);
                this.optionList.statusList = getArrListByObj(data.select_list.status, true);

                this.optionList.workerList = this.formatMemberUser(data.select_list.member_user);

                this.optionList.workUserList = getArrListByObj(this.formatMemberUser(data.select_list.member_user));

                this.languageList = data.select_list.language_list;

                //数据渲染
                this.tableData = data.data.value;

                this.tableData.map((item) => {
                    return item.opera_log.reverse();
                });

                this.tableKey = data.data.key;
                this.tableProps = [
                    'index',
                    'spu',
                    'photo_path',
                    'good_name',
                    'category_path',
                    'sample_type',
                    'good_info',
                    'product_status',
                    'opera_log',
                    'opera_time',
                    'remarks'
                ];
                this.tableWidth = [
                    '60',
                    '150',
                    '80',
                    'good_name',
                    'category_path',
                    'sample_type',
                    'good_info',
                    '100',
                    '200',
                    '100',
                    'remarks'
                ];

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;
            }
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign');
            this.assignType = 1;

        },

        // 重新指派任务
        handleClickReAssign () {
            this.checkedStatus('isShowAssign');
            this.assignType = 2;
        },

        //勾选的id
        selectId () {
            return this.saveCheckInfo.map(item => item.spu[0]);
        },

        //确认指派
        handleConfirmAssign (val) {
            const {check: assigned_user} = val;
            const spu_list = this.selectId();

            if (!assigned_user) {
                return this.$message.warning('请选择指派人员!');
            }
            if (this.assignType === 1) {
                // 指派人员
                assignSpu({assigned_user, spu_list}).then(({data}) => {
                    if (data.status !== 1) {
                        return this.$message.error(data.errorMess);
                    }

                    this.$message.success('指派成功！');
                    this.handleQueryInfo();

                });
            } else if (this.assignType === 2) {
                // 重新指派人员
                reAssignSpu({assigned_user, spu_list}).then(({data}) => {
                    if (data.status !== 1) {
                        return this.$message.error(data.errorMess);
                    }
                    this.$message.success('指派成功！');
                });
            }
        },
        // 保存上传文件
        handleSaveFileList (file, row) {

            let formData = new FormData();
            formData.append('file_image_data', file.raw);

            upLoadPhoto(formData).then(({data}) => {
                if (data.status == 1) {
                    row.image_path = data.data.filePath;
                } else {
                    this.$message.error(data.errorMess);
                }
            });
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 驳回
        handleClickReject () {
            this.checkedStatus('isShowReject');
        },

        //驳回操作数据处理
        rejectTypeChange (row) {
            let list = row.rejectList;
            let data = list.find((item) => {
                return row.id === item.id;
            });

            row = Object.assign(row, data);
            row.reject_to_dpts = row.reject_to_dpts.split('/');

        },

        //获取驳回原因
        handleRejectInfo () {

            getRejectInfo().then(({data}) => {
                this.rejectList = data.data.list;
                this.rejectDefault = {
                    rejectList: this.rejectList,
                    id: '',
                    reject_type_text: '',
                    setting_introduce: '',
                    remark: '',
                    reject_to_dpt: '',
                    reject_to_dpts: [],
                    image_path: '',
                    remarks: '',
                };
                this.rejectData.push(Object.assign({}, this.rejectDefault));
            });
        },

        // 确认驳回
        handleConfirmReject () {

            this.rejectData.forEach((item) => {
                delete item.rejectList;
            });

            let formData = new FormData();
            formData.append('spus', JSON.stringify(this.selectId()));
            formData.append('reject_data_list', JSON.stringify(this.rejectData));

            rejectSubmit(formData).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功!');
                    this.isShowReject = false;
                    this.handleQueryInfo();
                    this.rejectData.length = 0;
                    this.rejectData.push(Object.assign({}, this.rejectDefault));
                } else {
                    this.$message.error('操作失败! 请检查填写是否正确!');
                }
            });

        },

        // 删除驳回行
        handleDeleteInput (idx) {
            this.rejectData.splice(idx, 1);
        },

        //添加一行
        handleAddInput (idx) {
            this.rejectData.splice(idx + 1, 0, Object.assign({}, this.rejectDefault));
        },

        // 提交
        handleClickSubmit () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.$confirm('确认提交？', '提示', {
                type: 'warning'
            }).then(() => {});
        },

        //小组分类
        searchGroup (value) {
            console.log(value);
        }
    },
    created () {

        try {
            if (JSON.stringify(this.copyEditListInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['copy_edit_list']);
                this.handleQueryInfoCallBack(this.copyEditListInfo);
            }
        } catch (e) {
            console.log(e);
        }

        this.handleRejectInfo();
    }
};
</script>

<style lang="less" scoped>
@upload-height: 185px;
@border-color: #aaa;

/deep/ .el-input, /deep/ .el-select {
    width: 100%;
}

.ui-table--supplier_code {
    font-size: 12px;
    line-height: 10px;

    p:nth-child(1) {
        /*font-weight: bold;*/
    }

    p:nth-child(2) {
        cursor: pointer;
        color: #3388ff;
    }

    p {
        margin-bottom: 8px;
    }
}

/deep/ .el-table td,
/deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #ffffff;
    top: -12px;
    right: -20px;
    font-weight: normal;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
        font-size: 14px;
        color: #6facff;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            border: 1px solid #e6e6e6;
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;
                background: #F5F9F8;

                span {
                    color: @border-color;
                    font-size: 34px;
                    margin-right: 0;
                    position: relative;
                    top: -8px
                }

                p {
                    position: absolute;
                    left: 33px;
                    top: 20px;
                    font-size: 12px;
                    color: #999999;
                }
            }
        }

        .ui-com_edit__upload--item--mask {
            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, 0.3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #ffffff;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

.ui-table__img img {
    max-width: 70px;
    max-height: 90px;
}
</style>



