<template>
    <div class="ui-qrcode_print">
        <!--<iframe name="myIframe" ref="myIframe" :src="`/home/qrCodeIndex?sku_list=${data}`" frameborder="0"></iframe>-->
    </div>
</template>

<script>
    import {
        printHtml
    } from '@/api/common'
    export default {
        name: "PrintQRCode",
        props: ['data'],
        data() {
            return {
                url: ''
            }
        },
        methods: {
            handlePrint() {
                // this.$refs.myIframe.focus();
                // this.$refs.myIframe.print();
                // window.frames['myIframe'].focus();
                // window.frames['myIframe'].print();
                // window.frames['myIframe'].document.onload = () => {
                //     console.log('123123');
                // }

                const iframe = document.createElement("iframe");
                iframe.name = 'myIframe';
                iframe.src = `/home/qrCodeIndex?sku_list=${this.data}`;
                if (iframe.attachEvent){
                    iframe.attachEvent("onload", function(){
                        alert("loaded");
                    });
                } else {
                    iframe.onload = function(){
                        printHtml({
                            html: window.frames['myIframe'].document.querySelector('html').innerHTML.replace(/alt=""/ig, '/'),
                            printer: 'Honeywell PC42d (203 dpi)'
                        }).then(({ data }) => {
                            console.log(data);
                        })
                    };
                }
                document.querySelector('.ui-qrcode_print').appendChild(iframe);
            }
        },
        watch: {
            data(val) {

            }
        }
    }
</script>

<style scoped lang>
    .ui-qrcode_print {
        position: absolute;
        z-index: -999;
        top: 0;
        left: 0;
        width: 66mm;
        height: 32mm;
    }
</style>


