<script>
import config from '@/assets/js/config';
import {
    getNumByTime,
    getArrListByObj
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';
import {
    getGoodsModificationListInfo,
    getModificationWorkerList,
    assignTask,
    getRejectInfo,
    modificationGoodsReject,
    submitModificationGoods
} from '@/api/design_department/artist_manage/goods_modification_list';

import { upLoadPhoto, getUserByfunction } from '@/api/common';

import ArtGoodsEditPage from './ArtGoodsEditPage';

export default {
    data () {
        return {

            //高级筛选
            filterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                type: '5',
                moduleType: '1',
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
            },

            optionList: {

                productListList: [],
                functionInfoList: {},
                statusList: {},
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
                operateUserList: [],
                workUserList: [],

                userTypeList: [
                    {
                        label: '美工一',
                        value: '5'
                    },
                    {
                        label: '美工二',
                        value: '7'
                    },
                    {
                        label: '美工三',
                        value: '8'
                    }
                ],
                moduleTypeList: [
                    {
                        label: '待分配',
                        value: '1'
                    },
                    {
                        label: '待修图',
                        value: '2'
                    },
                    {
                        label: '修图记录',
                        value: '3'
                    }
                ],
                workerList: {},

            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 上传列表
            saveFileList: [],

            //指派任务弹窗
            isShowAssign: false,

            //驳回
            isShowReject: false,
            rejectData: [],
            rejectList: [],
            rejectDefault: {},

            //编辑
            isShowEdit: false,
            saveEditRow: {},
            isLeader: 0,

        };
    },
    components: {
        ArtGoodsEditPage
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'goodsModificationListInfo'
        ]),
    },
    methods: {
        ...mapActions([
            'GetGoodsModificationListInfo'
        ]),

        // 操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.operateUseMax = 30;
                    break;
                case 'hours':
                    this.queryData.operateUseMax = 24;
                    break;
                case 'minutes':
                    this.queryData.operateUseMax = 60;
                    break;
            }
        },

        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.functionInfo}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },

        handleClickTest () {
            this.isShowEdit = true;
        },
        // 点击编辑
        handleClickEdit (row) {
            this.saveEditRow = row;
            this.isShowEdit = true;
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['goods_modification_list']);
            }

            let obj = {

                type: this.queryData.type,
                list_type: this.queryData.moduleType,
                spu: this.queryData.productSPU.split(' ').join(','),
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join('->'),
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function: this.queryData.functionInfo,
                user: this.queryData.workUser,
                opera_user: this.queryData.operateUser,
                opera_time_start: this.queryData.operateTime[0],
                opera_time_end: this.queryData.operateTime[1],
                status: this.queryData.auditState,
                opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
                ...params
            };

            this.GetGoodsModificationListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                type: '5',
                moduleType: '1',
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.spu[0]);
            });
            return idArr;
        },

        // 切换表格列表
        handleToggleTableUserInfo (val) {
            this.queryData.type = val;
            this.handleQueryInfo();
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
            this.handleQueryInfo();
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 保存上传文件
        handleSaveFileList (file, row) {

            let formData = new FormData();
            formData.append('file', file.raw);

            upLoadPhoto(formData).then(({data}) => {
                if (data.status == 1) {
                    row.image = data.data.path;
                } else {
                    this.$message.error(data.errorMess);
                }
            });
            console.log(row.image, 8899);
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'goods_modification_list',
                value: JSON.stringify(this.queryData)
            });

            //数据渲染
            this.isLeader = data.is_leader;
            this.tableData = data.data.value;

            this.tableData.map((item) => {
                return item.opera_log.reverse();
            });
            if (data.data) {
                this.tableProps = Object.keys(data.data.key);
                this.tableKey = Object.values(data.data.key);
                this.tableWidth = ['60', '150', '80', '200', '150', '80', '250', '120', '220', '120', ''];
            }
            if (data.select_list) {
                this.optionList.productListList = [...data.select_list.category];
                this.optionList.functionInfoList = getArrListByObj(data.select_list.function, true);
                this.optionList.statusList = getArrListByObj(data.select_list.status, true);
                if (data.select_list.user) {
                    this.optionList.workUserList = getArrListByObj(data.select_list.user);
                }
            }
            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign');
        },

        //确认指派
        handleConfirmAssign (val) {
            let obj = {
                spu: this.selectId().join(','),
                distr_user: val.check
            };
            assignTask(obj).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo();
                } else {
                    this.$message.error('操作失败');
                }
            });
        },

        // 驳回
        handleClickReject () {
            this.checkedStatus('isShowReject');
        },

        //驳回操作数据处理
        rejectTypeChange (row) {
            let list = row.rejectList;
            let data = list.find((item) => {
                return row.type === item.id;
            });
            row.setting_introduce = data.setting_introduce;
            row.remark = data.remark;

            row.reject_to_dpt = data.reject_to_dpt;
            row.reject_to_dpts = data.reject_to_dpts.split('/');

        },

        //获取驳回原因
        handleRejectInfo () {

            getRejectInfo({reject_from_dpt: 5}).then(({data}) => {
                this.rejectList = data.data.list;
                this.rejectDefault = {
                    rejectList: this.rejectList,
                    type: '',
                    setting_introduce: '',
                    reject_to_dpt: '',
                    reject_to_dpts: [],
                    remark: '',
                    image: ''
                };
                this.rejectData.push(Object.assign({}, this.rejectDefault));
            });
        },

        // 确认驳回
        handleConfirmReject () {
            let arr = [];
            this.rejectData.forEach((item) => {
                arr.push({
                    reject_reason: item.type,
                    reject_content: item.setting_introduce,
                    reject_image: item.image,
                    type: item.reject_to_dpt
                });
            });

            let obj = {
                spu: this.selectId().join(','),
                reject_data: arr
            };

            modificationGoodsReject(obj).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功!');
                    this.isShowReject = false;
                    this.handleQueryInfo();
                    this.rejectData.length = 0;
                    this.rejectData.push(Object.assign({}, this.rejectDefault));

                } else {
                    this.$message.error('操作失败! 请检查填写是否正确!');
                }
            });

        },

        // 删除驳回行
        handleDeleteInput (idx) {
            this.rejectData.splice(idx, 1);
        },

        //添加一行
        handleAddInput (idx) {

            this.rejectData.splice(idx + 1, 0, Object.assign({}, this.rejectDefault));
        },

        // 提交
        handleClickSubmit () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.$confirm('确认提交？', '提示', {
                type: 'warning'
            }).then(() => {
                submitModificationGoods({spu: this.selectId().join(',')}).then(({data}) => {
                    if (data.status === 1) {

                        this.$message.success('操作成功!');
                    } else {

                        this.$message.error('操作失败!');
                    }
                });
            });
        },

        //小组分类
        searchGroup (value) {
            console.log(value);
        },

    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.goodsModificationListInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['goods_modification_list']);
                this.handleQueryInfoCallBack(this.goodsModificationListInfo);
            }
        } catch (e) {
            console.log(e);
        }

        getModificationWorkerList().then(({data}) => {
            let obj = {};
            if (data.data) {
                data.data.forEach((item) => {
                    obj[item.user_number] = item.user_name;
                });
                this.optionList.workerList = obj;
            }
        });

        this.handleRejectInfo();

    }
};
</script>

<style lang="less" scoped>

@upload-height: 185px;
@border-color: #aaa;
/deep/ .el-input, /deep/ .el-select {
    width: 100%;
}

.artist-screen {
    background-color: white;

    .ul-artist {
        display: flex;
        font-size: 14px;
        font-weight: bold;
        border-bottom: 1px solid #dcdfe6;

        li {
            padding: 0px 15px;
            height: 40px;
            line-height: 40px;
            position: relative;
            cursor: pointer;
        }

        li:not(:last-child)::after {
            content: '';
            width: 1px;
            height: 14px;
            background-color: #cccccc;
            position: absolute;
            right: 0;
            top: 50%;
            margin-top: -7px;
        }

        .active-artist {
            color: #5196fe;
        }

        .active-artist::before {
            content: '';
            width: 100%;
            height: 2px;
            background-color: #5196fe;
            position: absolute;
            bottom: -2px;
            left: 0;
        }
    }

    .ul-status {
        display: flex;
        font-size: 12px;

        li {
            height: 30px;
            padding: 0px 15px;
            line-height: 30px;
            position: relative;
            cursor: pointer;
        }

        li:not(:last-child)::after {
            content: '';
            width: 1px;
            height: 12px;
            background-color: #cccccc;
            position: absolute;
            right: 0;
            top: 50%;
            margin-top: -6px;
        }

        .active-status {
            span {
                background-color: #5196fe;
                color: white;
                padding: 2px 5px;
                border-radius: 3px;
            }
        }

    }
}

.ui-table--supplier_code {
    font-size: 12px;
    line-height: 10px;

    p:nth-child(2) {
        cursor: pointer;
        color: #3388ff;
    }

    p:nth-child(1) {
        /*font-weight: bold;*/
    }

    p {
        margin-bottom: 8px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
    font-weight: normal;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
        font-size: 14px;
        color: #6facff;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: @upload-height;
        }

        .ui-com_edit__upload--last_btn {
            /*border: 1px dashed #cccccc;*/
            width: 100%;
            height: @upload-height;

            /deep/ .el-upload {
                display: block;
                height: @upload-height;
                line-height: @upload-height;
                background: #fafafa;
                border: 1px #e6e6e6 solid;

                span {
                    color: @border-color;
                    font-size: 34px;
                    margin-right: 0;
                    position: relative;
                    top: -8px
                }

                p {
                    position: absolute;
                    left: 33px;
                    top: 20px;
                    font-size: 12px;
                    color: #999999;
                }
            }
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: @upload-height;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

.ui-table__img img {
    max-width: 70px;
    height: 80px;
}
</style>



