/**  --->>>>   |^ 摄影样品扫码列表 ^|   <<<<---**/

import http from '@/http';
import CONFIG from '@/assets/js/config';

const PARAMS = {};

// 查询
export const getShotScanInfo = (params = PARAMS) => {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample/sample_scan/shot_display_codes', data);
};

// 提交送样
export const submitShotCodes = (params = PARAMS) => {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.postAjax('/sample/Sample_scan/shot_submit_codes', data);
};

//数量详情
export function getDetail (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };
    return http.getAjax('/sample/sample_scan/get_by_box_info', data);
}



