                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement
                            label="商品目录"
                            template="cascader"
                            :options="optionList.productList"
                            :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                            v-model="queryData.productList"
                        ></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业组员" template="search" :options="optionList.workUserList"
                                           v-model="queryData.workUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业节点" template="search" :options="optionList.functionInfoList"
                                           v-model="queryData.functionInfo" @searchChange="getOperateUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核状态" :isDisabled="!queryData.functionInfo" template="search"
                                           :options="optionList.statusList"
                                           v-model="queryData.auditState">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作用时">
                            <el-select :disabled="!queryData.functionInfo" size="small"
                                       v-model="queryData.operateUseType"
                                       @change="handleTimeRange(queryData.operateUseType)" clearable>
                                <el-option v-for="(label,value) in optionList.timeTypeList"
                                           :label="label"
                                           :value="value"
                                           :key="label">

                                </el-option>
                            </el-select>
                            <el-slider
                                :disabled="!queryData.functionInfo"
                                v-if="queryData.operateUseType"
                                v-model="queryData.operateUseTime"
                                range
                                :min="1"
                                :max="queryData.operateUseMax">
                            </el-slider>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员" :isDisabled="!queryData.functionInfo" template="search"
                                           :options="optionList.operateUserList"
                                           v-model="queryData.operateUser">
                        </SearchRequirement>
                    </li>
                    <transition
                        name="slide-fade"
                        @after-enter="animation()"
                        @after-leave="animation()"
                        @before-enter="animation(1)"
                    >
                        <div v-show="filterShow">
                            <li>
                                <SearchRequirement
                                    label="操作时间"
                                    template="daterange"
                                    v-model="queryData.operateTime"
                                ></SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button
                        size="small"
                        type="primary"
                        @click="handleQueryInfo({limit: pageData.currentList})"
                        icon="icon-chaxun"
                    >查询
                    </el-button>
                    <el-button size="small" type="text" @click="handleClickFilter" v-show="!filterShow">
                        高级筛选
                        <i class="el-icon-caret-bottom"></i>
                    </el-button>
                    <el-button size="small" type="text" @click="handleClickFilter" v-show="filterShow">
                        基本筛选
                        <i class="el-icon-caret-top"></i>
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
              <span class="ui-top_i" @click="showSearch = !showSearch">
                <ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon>
              </span>
        </div>
        <ComTableInfo
            isCheck
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            :other-height="['ui-fn-module']"
            @checked="handleSaveCheckInfo">

            <template slot="index" slot-scope="scope">
                <div>
                    <span :class="scope.row.reject_type === '1' ? 'el-icon-warning' : 'el-icon-success'"></span>
                    <p>{{autoAddTableInx(scope.$index)}}</p>
                </div>
            </template>

            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu[0]}}</p>
                    <p>
                        <el-popover
                            placement="right"
                            trigger="hover">
                            <ul class="ui-sku_open">
                                <li
                                    v-for="(item, index) in scope.row.sku.split(',')"
                                    @click="handleClickEdit(scope.row, item, index)">
                                    {{item}}{{scope.row.sku_name.split(',')[index]}}
                                </li>
                            </ul>
                            <span slot="reference">{{scope.row.spu[1]}}</span>
                        </el-popover>
                    </p>
                    <div class="ui-table--supplier_code--flag" :class="`ui-table_flag${scope.row.devp_code}`"
                         v-show="scope.row.devp_type">
                        <span>{{scope.row.devp_type}}</span>
                    </div>
                    <div class="ui-table--icon_btn" v-if="queryData.moduleType==='2'">
                        <span v-if="scope.row.distr_user_name" class="opera-name">{{scope.row.distr_user_name}}</span>
                        <i
                            :class="`icon-bianji${scope.row.status_code === '1' || scope.row.status_code === '4' ? '' : ' ui-disable'}`"
                            @click.stop="handleClickEdit(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="photo_path" slot-scope="scope">
                <LoadingImg :src="scope.row.photo_path" @click="handleLookImage(scope.row.photo_path)"></LoadingImg>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>
                    <p>
                        <span>{{scope.row.good_name[0]}}</span>
                    </p>
                    <p>
                        <span>{{scope.row.good_name[1]}}</span>
                    </p>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.category_path" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="sample_type" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.sample_type" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.good_info" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
                <div class="ui-table--icon_btn">
                    <el-popover
                        placement="bottom"
                        title="采样链接"
                        width="200"
                        trigger="click">
                        <ul>
                            <li v-for="item in scope.row.link">
                                <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                            </li>
                        </ul>
                        <el-button slot="reference" type="text"><i class="icon-caiyanglianjie"></i></el-button>
                    </el-popover>
                    <el-popover
                        placement="bottom"
                        title="参考链接"
                        width="200"
                        trigger="click">
                        <li v-for="item in scope.row.link_reference">
                            <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                        </li>
                        <el-button slot="reference" type="text"><i class="icon-cankaolianjie"></i></el-button>
                    </el-popover>
                </div>
            </template>
            <template slot="product_status" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.product_status" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>
            <template slot="opera_log" slot-scope="scope">
                <div v-for="(item, index) in scope.row.opera_log" v-if="scope.row.opera_log.length<=3" :key="index">
                    {{item}}
                </div>
                <el-popover v-if="scope.row.opera_log.length>3"
                            style="cursor: pointer;"
                            placement="left"
                            trigger="hover">
                    <div v-for="(item, index) in scope.row.opera_log"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.opera_log" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.opera_log.length>3">...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="opera_time" slot-scope="scope">
                <div>
                    <p v-for="(item, key) of scope.row.opera_time" :key="key">
                        <span>{{item}}</span>
                    </p>
                </div>
            </template>

            <template slot="remarks" slot-scope="scope">
                <div>
                    <p v-if="scope.row.remarks &&index!==1" v-for="(item, index) in scope.row.remarks.split('；')">
                        {{item}}
                    </p>
                    <el-popover
                        style="cursor: pointer;"
                        placement="left"
                        trigger="hover">
                        <p v-if="scope.row.reject_reason" style="max-width:200px;">
                            <span>{{scope.row.reject_reason}}</span>
                        </p>
                        <div slot="reference">
                            <p v-if="scope.row.reject_reason" class="text-ellipsis">
                                <span>{{scope.row.reject_reason}}</span>
                            </p>
                        </div>
                    </el-popover>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal"
            ></el-pagination>
        </div>

        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <CopyEditPage :show.sync="isShowEdit" :spu="editSpu"></CopyEditPage>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import CopyEditPage from './CopyEditPage';
import {
    autoAddTableInx,
    getNumByTime,
    getArrListByObj
} from '@/services/index';
import { mapActions, mapGetters } from 'vuex';
import { getUserByfunction } from '@/api/common';

export default {
    data () {
        return {
            // 指派类别
            assignType: 1, // 1 指派， 2 重新指派；
            //高级筛选
            filterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                moduleType: '3', //1 待分配  2 待编辑  3 已编辑
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
            },

            optionList: {
                productList: [],
                functionInfoList: {},
                workUserList: {},
                statusList: {},
                moduleTypeList: [
                    {
                        label: '待分配',
                        value: '1'
                    },
                    {
                        label: '待编辑',
                        value: '2'
                    }
                ],
                workerList: {},
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
                operateUserList: [],
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0
            },

            // 编辑
            isEditShow: false,
            formLabelWidth: '110px',
            saveCheckId: '',

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 上传列表
            saveFileList: [],

            //文案小组和商品分类
            classSearch: [
                {
                    title: '全部',
                    number: 400
                },
                {
                    title: '手机周边',
                    number: 35
                },
                {
                    title: '全部',
                    number: 350
                },
                {
                    title: '服饰珠宝',
                    number: 50000
                },
                {
                    title: '服饰珠宝',
                    number: 5
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 50121212
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501213213
                }
            ],

            //指派任务弹窗
            isShowAssign: false,

            //驳回弹窗
            isShowReject: false,

            isShowEdit: false,
            editSpu: '',
            languageList: {}
        };
    },
    components: {
        CopyEditPage
    },
    computed: {
        ...mapGetters(['queryParamsInfo', 'copyEditRecordInfo'])
    },
    methods: {
        ...mapActions(['GetCopyEditRecordInfo']),
        autoAddTableInx,

        // 操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.operateUseMax = 30;
                    break;
                case 'hours':
                    this.queryData.operateUseMax = 24;
                    break;
                case 'minutes':
                    this.queryData.operateUseMax = 60;
                    break;
            }
        },
        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.functionInfo}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },

        handleClickTest () {
            this.isShowEdit = true;
        },
        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['copy_edit_record']);
            }

            let obj = {
                spu: this.queryData.productSPU, //SPU
                spu_name: this.queryData.productName, //SPU名称
                supplier_code: this.queryData.supplierCode, //供应商代码
                supplier_name: this.queryData.supplierName, //供应商名称
                category_id: this.queryData.productList, //分类
                function: this.queryData.functionInfo, //职能
                audit_status: this.queryData.auditState, //审核状态
                operator: this.queryData.workUser, //操作人员
                operation_use_time: '', //操作用时
                operation_time_start: this.queryData.operateTime[0], //操作开始
                operation_time_end: this.queryData.operateTime[1], //操作结束
                query_type: this.queryData.moduleType, //1 待分配  2 待编辑  3 已编辑
                opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
            };

            this.GetCopyEditRecordInfo(obj)
                .then(({data}) => {
                    console.log(data, 99);

                    this.handleQueryInfoCallBack(data);
                })
                .catch(err => {
                    console.log(err);
                });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                moduleType: this.queryData.moduleType,
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo(
                {
                    limit: val,
                    offset: 1
                },
                true
            );
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo(
                {
                    limit: this.pageData.currentList,
                    offset: val
                },
                true
            );
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
            this.handleQueryInfo();
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 点击编辑
        handleClickEdit (row) {
            this.isShowEdit = true;
            this.editSpu = row.spu[0];
        },

        // 格式化用戶列表數據
        formatMemberUser (originSource) {
            let targetSource = {};
            for (let key in originSource) {
                targetSource = {
                    ...targetSource,
                    [key]: originSource[key]['user_name']
                };
            }
            return targetSource;
        },
        //查询回调
        handleQueryInfoCallBack (data) {
            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'copy_edit_record',
                value: JSON.stringify(this.queryData)
            });

            if ('select_list' in data) {
                // 渲染下拉
                this.optionList.productList = [...data.select_list.category_list];
                this.optionList.functionInfoList = getArrListByObj(data.select_list.function, true);
                this.optionList.statusList = getArrListByObj(data.select_list.status, true);

                this.optionList.workerList = this.formatMemberUser(data.select_list.member_user);

                this.optionList.workUserList = getArrListByObj(this.formatMemberUser(data.select_list.member_user));

                this.languageList = data.select_list.language_list;

                //数据渲染
                this.tableData = data.data.value;
                this.tableData.map((item) => {
                    return item.opera_log.reverse();
                });

                this.tableKey = data.data.key;
                this.tableProps = [
                    'index',
                    'spu',
                    'photo_path',
                    'good_name',
                    'category_path',
                    'sample_type',
                    'good_info',
                    'product_status',
                    'opera_log',
                    'opera_time',
                    'remarks'
                ];
                this.tableWidth = [
                    '60',
                    '150',
                    '80',
                    'good_name',
                    'category_path',
                    'sample_type',
                    'good_info',
                    '100',
                    '200',
                    '100',
                    'remarks'
                ];

                //渲染分页
                this.pageData.currentList = data.page.limit * 1;
                this.pageData.currentPage = data.page.offset * 1;
                this.pageData.currentTotal = data.page.total * 1;
            }
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        //勾选的id
        selectId () {
            return this.saveCheckInfo.map(item => item.spu[0]);
        },

        //小组分类
        searchGroup (value) {
            console.log(value);
        }
    },
    created () {
        this.thisVm = this;

        try {
            if (JSON.stringify(this.copyEditRecordInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['copy_edit_record']);
                this.handleQueryInfoCallBack(this.copyEditRecordInfo);
            }
        } catch (e) {
            console.log(e);
        }
    }
};
</script>

