<template>
    <div>
        <el-checkbox-group :disabled="disabled" v-model="modelForm"
                           v-if="type === 'checkbox'">
            <el-checkbox
                    :key="item.id"
                    :label="item.id"
                    v-for="item in options">{{item.attribute_value_cn}}</el-checkbox>
        </el-checkbox-group>
        <el-radio-group :disabled="disabled" v-model="modelForm" v-if="type === 'radio'">
            <el-radio :label="item.id" v-for="(item, index) in options" :key="index">{{item.attribute_value_cn}}</el-radio>
        </el-radio-group>
    </div>
</template>

<script>
    export default {
        name: "SKUAttr",
        model: {
            prop: 'model',
            event: 'changes'
        },
        props: {
            disabled: {
                type: Boolean,
                default: false
            },
            type: {
                type: String,
                default: 'checkbox'
            },
            model: {
                required: true,
                type: [String, Array],
                default() {
                    return this.type === 'checkbox' ? [] : '';
                }
            },
            options: {
                required: true,
                type: [Array, Object],
                default() {
                    return []
                }
            }
        },
        data() {
            return {
                modelForm: ''
            }
        },
        created() {
            if(this.type === 'radio' && this.model.join) {
                this.modelForm = this.model.join('');
            } else {
                this.modelForm = this.model;
            }
        },
        watch: {
            modelForm(val) {
                this.$emit('changes', val);
            },
            model() {
                if(this.type === 'radio' && this.model.join) {
                    this.modelForm = this.model.join('');
                } else {
                    this.modelForm = this.model;
                }
            }
        }
    }
</script>

<style scoped lang="less">
    /deep/ .el-radio {
        margin: 0 20px 0 0!important;
    }
</style>


