<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.productList" label="商品目录" template="cascader"
                                           :options="optionList.productLists"
                                           :props="{label: 'category_name', value: 'id', children: 'child_id'}">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商">
                            <el-input size="small" v-model="queryData.supplier"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.buyType" label="购买类型" template="select"
                                           :options="optionList.buyTypeList">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业节点" template="search" :options="optionList.functionList"
                                           v-model="queryData.function" @searchChange="getOperateUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员" :isDisabled="!queryData.function" template="search"
                                           :options="optionList.operateUserList"
                                           v-model="queryData.opeUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement v-model="queryData.opeTime" label="操作时间" template="daterange">
                        </SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <!--<el-button-->
            <!--v-if="item.method && item.permit(thisVm)"-->
            <!--v-for="item in buttonList"-->
            <!--size="small"-->
            <!--:type="item.type"-->
            <!--:key="item.value"-->
            <!--@click="item.action(thisVm, item.method)">{{item.name}}</el-button>-->
            <el-button type="text" size="small" @click="handleCreateOrder">
                <i class="icon-shengchengdingdan icon-plain"></i>生成订单
            </el-button>
            <el-button type="text" size="small" @click="handleOperationFn(1, '取消买样')">
                <i class="icon-quxiaomaiyang icon-main"></i>取消买样
            </el-button>
            <el-button type="text" size="small" @click="handleOperationFn(2, '刷新金额')">
                <i class="icon-quxiaomaiyang icon-main"></i>刷新金额
            </el-button>
            <el-button type="text" size="small" @click="handleOperationFn(3, '推送付款')">
                <i class="icon-quxiaomaiyang icon-main"></i>推送付款
            </el-button>
            <el-button type="text" size="small" @click="handleTransferShow">
                <i class="icon-renwuzhipai icon-warn"></i>任务指派
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu}}</p>
                    <div class="ui-table--supplier_code--flag ui-table_flag6"
                         v-if="scope.row.sku_list && scope.row.sku_list[0].is_customized === '1'">
                        <span>定制</span>
                    </div>
                </div>
            </template>
            <template slot="sku_list" slot-scope="scope">
                <p v-for="(item, index) in scope.row.sku_list" :style="{marginTop: index && '5px'}"
                   v-if="scope.row.sku_list.length<=3">{{`${item.sku}
                    ${item.attribute_value_cn}
                    ${item.reference_price}`}}</p>
                <el-popover
                    v-if="scope.row.sku_list.length>3"
                    style="cursor: pointer;"
                    placement="left"
                    trigger="hover">
                    <p v-for="(item, index) in scope.row.sku_list" :style="{marginTop: index && '5px'}">{{`${item.sku}
                        ${item.attribute_value_cn}
                        ${item.reference_price}`}}</p>
                    <div slot="reference">
                        <p v-for="(item, index) in scope.row.sku_list" v-if="index<3"
                           :style="{marginTop: index && '5px'}">
                            {{`${item.sku}
                            ${item.attribute_value_cn}
                            ${item.reference_price}`}}</p>
                        <div>...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="dev_imgs" slot-scope="scope">
                <LoadingImg :src="scope.row.dev_imgs" @click="handleLookImage(scope.row.dev_imgs)"></LoadingImg>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div v-if="scope.row.category_path">
                    <div v-for="item in scope.row.category_path.split('->')">{{item}}</div>
                </div>
            </template>

            <template slot="sample_datum" slot-scope="scope">
                <div>
                    <p><span>购买类型：</span> <span>{{scope.row.sample_type}}</span></p>
                    <p><span>供应商编码：</span> <span>{{scope.row.supplier_code}}</span></p>
                    <p><span>供应商名称：</span> <span>{{scope.row.supplier_name}}</span></p>
                    <p><span>联系人/电话：</span> <span>{{scope.row.phone}}</span></p>
                    <div class="ui-table--icon_btn">
                        <el-popover
                            placement="bottom"
                            title="采样链接"
                            width="200"
                            trigger="click">
                            <a :href="scope.row.link" target="_blank" class="ui-link">{{scope.row.link}}</a>
                            <el-button slot="reference" type="text"><i class="icon-caiyanglianjie"></i></el-button>
                        </el-popover>
                        <el-popover
                            placement="bottom"
                            title="参考链接"
                            width="200"
                            trigger="click">
                            <a :href="scope.row.link_reference" target="_blank"
                               class="ui-link">{{scope.row.link_reference}}</a>
                            <el-button slot="reference" type="text"><i class="icon-cankaolianjie"></i></el-button>
                        </el-popover>
                    </div>
                </div>
            </template>
            <template slot="log_time" slot-scope="scope">
                <div v-for="(item, index) in scope.row.log_time" v-if="scope.row.log_time.length<=3" :key="index">
                    {{item}}
                </div>
                <el-popover v-if="scope.row.log_time.length>3"
                            style="cursor: pointer;"
                            placement="left"
                            trigger="hover">
                    <div v-for="(item, index) in scope.row.log_time"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.log_time" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.log_time.length>3">...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="order_info" slot-scope="scope">
                <div>
                    <p><span>po：</span>{{scope.row.sku_list && scope.row.sku_list[0].po}}</p>
                    <p><span>订单号：</span>{{scope.row.sku_list && scope.row.sku_list[0].order_number}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div>
                    <p><span>收款人：</span>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><span>账号：</span>{{scope.row.receive_account}}</p>
                    <p><span>店铺：</span>{{scope.row.shop_name}}</p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <el-dialog
            class="ui-layout_edit-dialog"
            custom-class="ui-com_edit"
            title="生成订单"
            width="1000px"
            top="5vh"
            :visible.sync="isAddShow"
            @close="handleCancelSubmitOrder">
            <div class="ui-create_order">
                <div class="ui-top_info">
                    <ul>
                        <li>
                            <span>收货人：</span>
                            <span>{{addOrderInfo.user_info.consignee}}</span>
                        </li>
                        <li>
                            <span>收货信息：</span>
                            <span>{{addOrderInfo.user_info.receipt_info}}</span>
                        </li>
                        <li>
                            <span>联系电话：</span>
                            <span>{{addOrderInfo.user_info.phone}}</span>
                        </li>
                    </ul>
                </div>
                <div class="ui-table_title">
                    <table>
                        <thead>
                        <tr>
                            <th></th>
                            <th>单价（元）</th>
                            <th>数量</th>
                            <th>金额（元）</th>
                        </tr>
                        </thead>
                    </table>
                </div>
                <div class="ui-table_content" v-for="(supItem, supInx) in addOrderInfo.spu_info" :key="supInx">
                    <div class="ui-table_content--title"><span>供应商名称：{{supItem.supplier_name}}</span></div>
                    <table>
                        <tbody>
                        <tr v-for="(item, index) in handleErgodicOrderInfo(submitCreateData[supItem.supplier_name], supItem)">
                            <td>
                                <div class="ui-table_content--item ui-media clearFix">
                                    <div class="ui-media--img">
                                        <img :src="item.skuImageUrl_min" alt="">
                                    </div>
                                    <div class="ui-media--content">
                                        <p>{{item.title_cn}}</p>
                                        <span>{{item.attribute_value_cn}}</span>
                                        <div class="ui-media--content--icon" :style="{
                                            backgroundImage: `url(${item.sku_image_url || saveActiveLink[supInx + '-' +index]})`}">
                                            <!--<img :src="item.link" alt="" @click="handleLookImage(item.link)"><i class="el-icon-d-arrow-right"></i>-->
                                            <!--<i v-if="!isCheckLink" class="icon-dianpumingcheng1" style="color: #ff6000"></i>-->
                                            <!--<i class="el-icon-d-arrow-right"></i>-->
                                            <transition mode="in-out" name="slide-fade">
                                                <ul class="ui-media--content--down clearFix">
                                                    <li class="ui-not_copy"
                                                        :class="checkProductInfo[`${supInx}-${index}-${i}`] && 'ui-active'"
                                                        v-for="(attr, i) in item.alibaba_atr.data.sku"
                                                        @click="handleCheckProduct(`${supInx}-${index}`, i, item, attr, supItem.supplier_name,
                                                        !checkProductInfo[`${supInx}-${index}-${i}`])">
                                                        <img :src="attr.skuImageUrl_min" alt="">
                                                        <span>{{attr.sku_atr[0].attribute_value}}</span>
                                                    </li>
                                                </ul>
                                            </transition>
                                        </div>
                                    </div>
                                </div>
                            </td>
                            <td>
                                <div class="ui-table_content--item">
                                    <span>{{item.unit_price}}</span>
                                </div>
                            </td>
                            <td>
                                <div class="ui-table_content--item">
                                    <span v-if="supItem.order_number">{{item.count}}</span>
                                    <el-input v-if="!supItem.order_number" size="small"
                                              v-model="countList[supInx + '-' +index]"
                                              @blur="handleCountPrice(supInx + '-' +index, item.unit_price)"></el-input>
                                </div>
                            </td>
                            <td>
                                <div class="ui-table_content--item">
                                    <span>{{submitCreateData[supItem.supplier_name] ? item.sum_price : priceList[supInx + '-' +index]}}</span>
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                    <div class="ui-table_content--row_count">
                        <div>
                            <span>运费共计：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].shippingFee : supItem.freight_price_total}}</span>
                            <span>优惠金额：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].discount : supItem.discounts_price}}</span>
                            <span>PO号：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].po : supItem.po}}</span>
                        </div>
                        <div>
                            <span>总额：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].totalAmount : supItem.price_total}}</span>
                            <span>商品金额：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].sumProductPayment : supItem.goods_price}}</span>
                            <span>订单号：{{
                                submitCreateData[supItem.supplier_name] ?
                                submitCreateData[supItem.supplier_name].order_id : supItem.order_number}}</span>
                        </div>
                    </div>
                </div>
                <div class="ui-table_footer clearFix">
                    <p>共计商品：{{addOrderInfo.user_info.sum_count}}件</p>
                    <p>合计总额：<span class="ui-total_amt">{{addOrderInfo.user_info.sum_total}}元</span></p>
                </div>
            </div>
            <div slot="footer">
                <el-button size="small" @click="handleCancelSubmitOrder">取消</el-button>
                <el-button size="small" type="primary" @click="handleSubmitCreateOrder"
                           v-if="isNotSubmit">
                    提交
                </el-button>
            </div>
        </el-dialog>
        <ComDialogRemarks :visible.sync="isTransferShow" formLabel="指派给" title="任务指派" type="select"
                          :option="optionList.userList" @confirm="handleConfirmTransfer"></ComDialogRemarks>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage,
    getArrListByObj
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';
import {
    getSamplePurchaseDetailInfo,
    submitCreateOrder,
    samplePurchaseOperation,
    workTransfer
} from '@/api/sample_track_manage/sample_track_module/sample_purchase';
import { getUserByfunction } from '@/api/common';

export default {
    data () {
        return {

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                productList: [],
                supplier: '',
                buyType: '',
                productName: '',
                opeUser: '',
                opeTime: '',
                function: '',
            },

            optionList: {
                buyTypeList: {},
                functionList: {},
                operateUserList: {},
                productLists: [],
                userList: {}
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 生成订单
            isAddShow: false,
            addOrderInfo: {
                user_info: {},
                spu_info: []
            },

            //保存激活图片链接
            saveActiveLink: {},

            //判断是否勾选图片
            isCheckLink: false,
            checkProductInfo: {},
            countList: {},
            priceList: {},
            submitCreateData: {},

            isTransferShow: false,

            isNotSubmit: false
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'samplePurchaseInfo'
        ])
    },
    methods: {
        ...mapActions([
            'GetSamplePurchaseInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.function}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },
        //转换格式
        getDevImg (img) {
            let arr = JSON.parse(img);
            return arr;
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_purchase']);
            }

            let obj = {
                spu: this.queryData.productSPU,
                supplier_name: this.queryData.supplier,
                category_id: this.queryData.productList,
                supplier_code: this.queryData.supplierName,
                sample_type: this.queryData.buyType,
                title_cn: this.queryData.productName,
                function: this.queryData.function,
                opera_user: this.queryData.opeUser,
                opera_time_start: this.queryData.opeTime[0],
                opera_time_end: this.queryData.opeTime[1],
                is_get_base: 1,
                ...params
            };

            this.GetSamplePurchaseInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                productList: [],
                supplier: '',
                buyType: '',
                productName: '',
                opeUser: '',
                opeTime: '',
            };
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 操作
        handleOperationFn (type, msg) {

            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            this.$confirm(`确认${msg}`, '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {};
                if (type === 1) {
                    let spu_po_arr = [];
                    this.saveCheckInfo.forEach(value => {
                        spu_po_arr.push({
                            po: value.sku_list[0].po,
                            spu: value.spu,
                            order_id: value.sku_list[0].order_id
                        });
                    });

                    obj = {
                        status: type,
                        spu_po_arr: JSON.stringify(spu_po_arr)
                    };
                } else {
                    let [spu, order] = [[], []];
                    this.saveCheckInfo.forEach(value => {
                        spu.push(value.spu);
                        order.push(value.sku_list[0].order_number);
                    });

                    obj = {
                        status: type,
                        spu: JSON.stringify(spu),
                        order_id: JSON.stringify(order)
                    };
                }
                samplePurchaseOperation(obj).then(({data}) => {
                    this.handleOperationCallBack(data);
                });
            });
        },

        // 计算金额
        handleCountPrice (key, price) {
            this.priceList[key] = this.countList[key] * price;
        },

        // 遍历生成订单信息
        handleErgodicOrderInfo (complete, supItem) {
            if (!complete) return supItem.sku_list;
            this.isNotSubmit = false;
            return supItem.sku_list.map(value => {
                return {
                    ...value,
                    ...complete.spu_info[value.sku]
                };
            });
        },

        // 生成订单
        handleCreateOrder () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            let isNot = false;
            let isCreate = [];

            for (let item of this.saveCheckInfo) {
                let itemOrder = item.sku_list[0].order_number;
                if (!!this.saveCheckInfo[0].sku_list[0].order_number !== !!itemOrder) {
                    isNot = true;
                }

                if (itemOrder) {
                    isCreate.push(itemOrder);
                }
            }

            if (isNot) {
                // isCreate.shift();
                this.$message.warning(`${isCreate.join(',')}已提交过，不能重复提交`);
                return;
            }

            let [spu, is_customized, order_id] = [[], [], []];
            this.saveCheckInfo.forEach(value => {
                spu.push(value.spu);
                is_customized.push(value.sku_list && value.sku_list[0].is_customized);
                order_id.push(value.sku_list && value.sku_list[0].order_id);
            });
            getSamplePurchaseDetailInfo({
                spu: spu.join(','),
                is_customized: is_customized.join(','),
                order_id: order_id.join(',')
            }).then(({data}) => {
                if (data.status) {
                    this.addOrderInfo = data.data;
                    this.isAddShow = true;
                    data.data.spu_info.forEach((supItem, supInx) => {
                        supItem.sku_list.forEach((item, inx) => {
                            this.$set(this.countList, `${supInx}-${inx}`, item.count);
                            this.$set(this.priceList, `${supInx}-${inx}`, item.sum_price);
                        });
                    });

                    this.isNotSubmit = this.submitCreateData === '{}' || this.addOrderInfo.spu_info[0] && !this.addOrderInfo.spu_info[0].order_number;
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            });
        },

        // 勾选产品
        handleCheckProduct (key, i, item, attr, supplierName, isCheck) {
            let obj = '';
            if (isCheck) {
                obj = {
                    offerId: item.alibaba_atr.data.productID,
                    specId: attr.specId,
                    supplierName,
                    sku: item.sku,
                    spu: item.spu,
                    link: attr.skuImageUrl_min
                };
                this.isCheckLink = true;
            } else {
                this.isCheckLink = false;
            }
            item.alibaba_atr.data.sku.forEach((value, inx) => {
                delete this.checkProductInfo[`${key}-${inx}`];
            });
            this.$set(this.checkProductInfo, `${key}-${i}`, obj);
            this.$set(this.saveActiveLink, key, attr.skuImageUrl_min);
        },

        // 取消提交订单
        handleCancelSubmitOrder () {
            this.saveActiveLink = {};
            this.checkProductInfo = {};
            this.submitCreateData = {};
            this.isAddShow = false;
        },

        // 提交生成订单
        handleSubmitCreateOrder () {
            let spu = [];
            this.saveCheckInfo.forEach(value => {
                spu.push(value.spu);
            });
            let skuArr = [];
            for (let [key, value] of Object.entries(this.checkProductInfo)) {
                let inxKey = key.replace(/-[0-9]+$/, '');
                skuArr.push({
                    ...value,
                    count: this.countList[inxKey]
                });
            }

            let allLength = 0;
            this.addOrderInfo.spu_info.forEach(value => {
                allLength += value.sku_list.length;
            });

            if (allLength !== skuArr.length) {
                this.$message.warning('请选择商品规格！');
                return;
            }

            let obj = {
                spu: spu.join(','),
                fullName: this.addOrderInfo.user_info.consignee,
                address: this.addOrderInfo.user_info.receipt_info,
                mobile: this.addOrderInfo.user_info.phone,
                checkInfo: JSON.stringify(skuArr)
            };
            submitCreateOrder(obj).then(({data}) => {
                // this.handleOperationCallBack(data);
                this.submitCreateData = data.data;
                if (data.status) {
                    this.submitCreateData = data.data;
                    this.$message.success('操作成功！');
                } else {
                    this.$message.error(data.errorMess || '操作失败！');
                }
            });
        },

        // 指派
        handleTransferShow () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.isTransferShow = true;
        },

        // 确认指派
        handleConfirmTransfer ({check}) {
            let spu = [];
            this.saveCheckInfo.forEach(value => {
                spu.push(value.spu);
            });
            // check
            let obj = {
                spu: spu.join(','),
                distr_user: check
            };
            workTransfer(obj).then(({data}) => {
                this.handleOperationCallBack(data);
            });
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                if (this.isAddShow) {
                    this.isAddShow = false;
                }
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_purchase',
                value: JSON.stringify(this.queryData)
            });

            // 渲染下拉
            this.optionList.buyTypeList = data.drop_down_box.buy_list;
            this.optionList.functionList = getArrListByObj(data.drop_down_box.function);
            this.optionList.productLists = data.drop_down_box.category_list.data;

            let userObj = {};
            if (data.group_user) {
                data.group_user.forEach(value => {
                    userObj[value.user_staff_id] = value.user_name;
                });
            }
            this.optionList.userList = userObj;

            //数据渲染
            this.tableData = data.data.data_list.value;
            this.tableKey = [...data.data.data_list.key];
            this.tableKey.unshift('序号');

            this.tableProps = ['index', 'spu', 'sku_list', 'dev_imgs', 'title_cn', 'category_path', 'sample_datum', 'order_info', 'log_time', 'remarks'];
            this.tableWidth = ['50', '100', '200', '80', '200', '120', '260', '200', '240', '200'];

            //渲染分页
            this.pageData.currentList = data.data.paging_data.limit * 1;
            this.pageData.currentPage = data.data.paging_data.offset * 1;
            this.pageData.currentTotal = data.data.paging_data.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.samplePurchaseInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['sample_purchase']);
                this.handleQueryInfoCallBack(this.samplePurchaseInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

