<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model.trim="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model.trim="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商类型">
                            <el-select v-model="queryData.supplierType" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.supplierTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="结算方式">
                            <el-select v-model="queryData.settlementType" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.settlementTypeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="开发人" template="search" :options="optionList.devUserList"
                                           v-model="queryData.devUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="发货地址">
                            <el-cascader
                                change-on-select
                                v-model="queryData.deliverAddress"
                                size="small"
                                placeholder="请选择"
                                :options="optionList.deliverAddressList"
                                filterable
                                :props="{value: 'city_code', label: 'city_name'}"
                                @change="handleSelectDeliver"
                            ></el-cascader>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="支付方式">
                            <el-select v-model="queryData.paymentMode" placeholder="请选择" size="small">
                                <el-option
                                    v-for="(label, value) in optionList.paymentModeList"
                                    :key="value"
                                    :label="label"
                                    :value="value"></el-option>
                            </el-select>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="联络人">
                            <el-input size="small" v-model="queryData.liaisons"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="创建人" template="search" :options="optionList.createUserList"
                                           v-model="queryData.createUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="创建日期" template="daterange"
                                           v-model="queryData.createTime"></SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <el-button @click="handleExportListInfo('_this')" type="text" size="small">
                <i class="icon-daochu icon-main"></i>导出当前页
            </el-button>
            <el-button @click="handleExportListInfo('_all')" type="text" size="small">
                <i class="icon-daochu icon-main"></i>导出全部
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <template slot="supplier_code" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.supplier_code}}</p>
                    <div class="ui-table--supplier_code--flag"
                         :class="scope.row.type === '生产商' ? 'ui-table_flag1' : 'ui-table_flag3'">
                        <span>{{scope.row.type.replace('商', '')}}</span>
                    </div>
                </div>
            </template>
            <template slot="supplier_name" slot-scope="scope">
                <div>
                    <p>{{scope.row.supplier_name}}</p>
                    <div class="ui-table--icon_btn">
                        <el-popover
                            placement="bottom"
                            title="供应商链接"
                            width="200"
                            trigger="click">
                            <a :href="scope.row.web_link" target="_blank" class="ui-link">{{scope.row.web_link}}</a>
                            <el-button slot="reference" type="text"><i class="icon-gongyingshanglianjie"></i>
                            </el-button>
                        </el-popover>
                        <i class="icon-tianyancha" @click="handlePreviewDetail(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="shipping" slot-scope="scope">
                <div>
                    <p>{{`${scope.row.shipping_province}${scope.row.shipping_city}`}}</p>
                    <p>{{`${scope.row.shipping_area}${scope.row.shipping_address}`}}</p>
                </div>
            </template>
            <template slot="contacts_info" slot-scope="scope">
                <div>
                    <p><i class="icon-ren icon-plain"></i>{{`${scope.row.contacts}/${scope.row.mobile}`}}</p>
                    <p><i class="icon-QQ icon-main"></i><a target="_self"
                                                           :href="`tencent://message/?uin=${scope.row.qq}`">{{scope.row.qq}}</a>
                    </p>
                    <p><i class="icon-weixin icon-wx"></i>{{scope.row.wechat}}</p>
                    <p><i class="icon-youxiang icon-danger"></i>{{scope.row.email}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div v-if="scope.row.pay_method === '线上支付宝'">
                    <p><span>方式：</span>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}
                    </p>
                    <p><span>账号：</span>{{scope.row.receive_account}}</p>
                    <p><span>名称：</span>{{scope.row.receive_name}}</p>
                    <p><span>店铺：</span>{{scope.row.shop_name}}</p>
                </div>
                <div v-else-if="scope.row.pay_method === '线下境外'">
                    <p><span>方式：</span>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}
                    </p>
                    <p><span>收款人：</span>{{`${scope.row.receive_name}/${scope.row.receive_mobile}`}}</p>
                    <p><span>账号：</span>{{scope.row.receive_account}}</p>
                    <p><span>身份证：</span>{{scope.row.receive_id_number}}</p>
                </div>
                <div v-else-if="scope.row.pay_method === '线下境内'">
                    <p><span>方式：</span>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}
                    </p>
                    <p><span>公司：</span>{{scope.row.receive_name}}</p>
                    <p><span>账号：</span>{{scope.row.receive_account}}</p>
                    <p><span>银行：</span>{{scope.row.receive_bank}}</p>
                </div>
            </template>
            <template slot="operate_dev_user" slot-scope="scope">
                <div>
                    <p v-if="scope.row.devlop_user"><span>开发人：</span><span>{{scope.row.devlop_user}}</span></p>
                    <p v-if="scope.row.create_user"><span>创建人：</span><span>{{scope.row.create_user}}</span></p>
                    <p v-if="scope.row.check_user"><span>审核人：</span><span>{{scope.row.check_user}}</span></p>
                </div>
            </template>
            <template slot="file_url" slot-scope="scope">
                <ul class="ui-supplier__img clearFix">
                    <!--fileTypeList-->
                    <li v-for="(name, key) in fileTypeList" v-if="scope.row[key]">
                        <div class="ui-supplier__img--area" @click="handleLookImage(scope.row[key])">
                            <div class="ui-supplier__img--item">
                                <div class="ui-supplier__img--main"
                                     :style="{background:'url('+scope.row[key]+') no-repeat center/contain'}">
                                </div>
                            </div>
                        </div>
                        <span class="ui-supplier__img__label">{{ name }}</span>
                    </li>
                </ul>
            </template>
            <template slot="status" slot-scope="scope">
                <div v-if="scope.row.status_code == '2'|| scope.row.status_code == '5'"><span
                    class="ui-state_circle state_orange"></span>{{scope.row.status}}
                </div>
                <div v-if="scope.row.status_code == '1'|| scope.row.status_code == '3'"><span
                    class="ui-state_circle state_blue"></span>{{scope.row.status}}
                </div>
                <div v-if="scope.row.status_code == '4'|| scope.row.status_code == '6'"><span
                    class="ui-state_circle state_red"></span>{{scope.row.status}}
                </div>
            </template>
            <template slot="operation" slot-scope="scope">
                <el-button type="text" @click="handleOperaiontLog(scope.row)" size="small">日志</el-button>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <ComDialogRemarks :visible.sync="isShowAudit" title="审核" @confirm="handleConfirmAudit"></ComDialogRemarks>
        <ComDialogRemarks :visible.sync="isShowReject" title="驳回" @confirm="handleConfirmReject"></ComDialogRemarks>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <QueryPreview :show.sync="isQueryPreview" :detail="queryPreviewData"></QueryPreview>
        <OperationLog :show.sync="isOpeLogShow" :id="saveCheckId" :userId="saveUserId"></OperationLog>
    </div>
</template>

<script>
import QueryPreview from './SupplierCreateList/QueryPreview';
import OperationLog from './SupplierCreateList/OperationLog';
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage,
    getArrListByObj
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getSupplierCreateListInfo
} from '@/api/dev_department/supplier_development/supplier_create_list';
import {
    getCityInfo,
    getSupplierDetailInfo
} from '@/api/login';

export default {
    components: {
        QueryPreview,
        OperationLog
    },
    data () {
        return {

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: ''
            },

            optionList: {
                supplierTypeList: {},
                settlementTypeList: {},
                devUserList: {},
                deliverAddressList: [],
                paymentModeList: {},
                liaisonsList: {},
                createUserList: {}
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 审核
            isShowAudit: false,

            // 驳回
            isShowReject: false,

            fileTypeList: {
                bl_url: '营业执照',
                tca_url: '收款协议',
                gtc_url: '纳税人证明',
                b_r_rul: '开票记录',
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 天眼查预览
            isQueryPreview: false,
            queryPreviewData: {},

            // 操作日志
            saveCheckId: '',
            saveUserId: '',
            isOpeLogShow: false,
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'supplierQualifiedInfo'
        ])
    },
    methods: {
        ...mapActions([
            'GetSupplierQualifiedListInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['supplier_qualified']);
            }

            let obj = {
                supplier_code: this.queryData.supplierCode.replace(/\s+/g, ','),
                type: this.queryData.supplierType,
                shipping_province: this.queryData.deliverAddress[0],
                shipping_city: this.queryData.deliverAddress[1],
                settle_method: this.queryData.settlementType,
                create_user: this.queryData.createUser,
                devlop_user: this.queryData.devUser,
                supplier_name: this.queryData.supplierName,
                contacts: this.queryData.liaisons,
                pay_method: this.queryData.paymentMode,
                start_time: this.queryData.createTime[0],
                end_time: this.queryData.createTime[1],
                ...params
            };

            console.log(obj, 'obj');
            this.GetSupplierQualifiedListInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: '',
            };
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 选择发货地址
        handleSelectDeliver (val) {
            let obj = {
                province_code: val[0]
            };
            for (let item of this.optionList.deliverAddressList) {
                if (item.value === val[0] && item.children.length) return;
            }
            getCityInfo(obj).then(({data}) => {
                this.optionList.deliverAddressList.map(value => {
                    if (value.city_code === val[0]) {
                        value.children = [
                            ...data.data
                        ];
                    }
                });
            });
        },

        // 操作日志
        handleOperaiontLog ({supplier_id, user_id}) {
            this.saveCheckId = supplier_id;
            this.saveUserId = user_id;
            this.isOpeLogShow = true;
        },

        // 导出明细信息
        handleExportListInfo (type) {
            if (type === '_all') {
                this.handleQueryInfo({
                    isExcel: 1,
                    export_type: 2
                });
            } else {
                this.handleQueryInfo({
                    isExcel: 1,
                    export_type: 1
                });
            }
        },

        // 天眼查数据预览
        handlePreviewDetail ({credit_code}) {
            getSupplierDetailInfo({
                credit_code: credit_code
            }).then(({data}) => {
                this.queryPreviewData = data.result;
                this.isQueryPreview = true;
            });
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 审核
        handleClickAudit () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.isShowAudit = true;
        },

        // 确认审核
        handleConfirmAudit (val) {
            console.log(val);
        },

        // 驳回
        handleClickReject () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.isShowReject = true;
        },

        // 确认驳回
        handleConfirmReject (val) {
            console.log(val);
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'supplier_qualified',
                value: JSON.stringify(this.queryData)
            });

            if (!this.optionList.deliverAddressList.length) {
                let deliverList = [];
                if (data.select_list.province_info) {
                    data.select_list.province_info.forEach(value => {
                        let obj = {
                            city_name: value.province_name,
                            city_code: value.province_code
                        };
                        deliverList.push({
                            ...obj,
                            children: []
                        });
                    });
                    this.optionList.deliverAddressList = [...deliverList];
                }
                this.handleSelectDeliver(this.queryData.deliverAddress);
            }

            // 渲染下拉
            this.optionList.supplierTypeList = data.select_list.sup_type;
            this.optionList.settlementTypeList = data.select_list.sup_settle_method;
            this.optionList.devUserList = getArrListByObj(data.select_list.devlop_user);
            this.optionList.paymentModeList = data.select_list.sup_pay_method;
            // this.optionList.liaisonsList = data.select_list.sup_pay_method;
            this.optionList.createUserList = getArrListByObj(data.select_list.create_user);
            this.optionList.stateList = data.select_list.sup_status;

            //数据渲染
            this.tableData = data.data.value;
            this.tableKey = [...data.data.key];
            this.tableKey.pop();
            this.tableKey.push('状态');
            this.tableKey.push('操作');
            this.tableWidth = ['50px', '100px', '', '150px', '180px', '180px', '180px', '280px', '80px', '100px'];

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
            this.tableProps = ['index', 'supplier_code', 'supplier_name', 'shipping', 'contacts_info', 'payment_info', 'operate_dev_user', 'file_url', 'status', 'operation'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.supplierQualifiedInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['supplier_qualified']);
                this.handleQueryInfoCallBack(this.supplierQualifiedInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>
@import (reference) '../../../../assets/less/index';

</style>



