                gatheringName: CONFIG.RULE_CONFIG.requiredBlur,
                gatheringAccount: CONFIG.RULE_CONFIG.requiredBlur,
                devUser: CONFIG.RULE_CONFIG.requiredBlur,
                // auditUser: CONFIG.RULE_CONFIG.requiredBlur,
                importDate: CONFIG.RULE_CONFIG.requiredBlur,
                gatheringPhone: CONFIG.RULE_CONFIG.requiredBlur,
                gatheringId: [
                    ...CONFIG.RULE_CONFIG.requiredBlur,
                    CONFIG.RULE_CONFIG.idNumber
                ],
                bankName: CONFIG.RULE_CONFIG.requiredBlur,
            },

            addFormItemConfig: {
                '1': [
                    {
                        label: '店铺名称',
                        key: 'shopName',
                        value: ''
                    },
                    {
                        label: '收款名称',
                        key: 'gatheringName'
                    },
                    {
                        label: '收款账号',
                        key: 'gatheringAccount',
                    },
                    {
                        label: '开发人',
                        key: 'devUser',
                        isSelect: true,
                        userList: {}
                    },
                    {
                        label: '审核人',
                        key: 'auditUser',
                    },
                    {
                        label: '导入日期',
                        key: 'importDate'
                    }
                ],
                '2': [
                    {
                        label: '收款人手机号',
                        key: 'gatheringPhone',
                        isLabelLayout: true
                    },
                    {
                        label: '收款名称',
                        key: 'gatheringName'
                    },
                    {
                        label: '收款账号',
                        key: 'gatheringAccount'
                    },
                    {
                        label: '收款人身份证',
                        key: 'gatheringId',
                        isLabelLayout: true
                    },
                    {
                        label: '开发人',
                        key: 'devUser',
                        isSelect: true,
                        userList: {}
                    },
                    {
                        label: '审核人',
                        key: 'auditUser'
                    },
                    {
                        label: '导入日期',
                        key: 'importDate'
                    },
                ],
                '3': [
                    {
                        label: '开户行名称',
                        key: 'bankName'
                    },
                    {
                        label: '收款名称',
                        key: 'gatheringName',
                    },
                    {
                        label: '收款账号',
                        key: 'gatheringAccount',
                    },
                    {
                        label: '开发人',
                        key: 'devUser',
                        isSelect: true,
                        userList: {}
                    },
                    {
                        label: '审核人',
                        key: 'auditUser',
                    },
                    {
                        label: '导入日期',
                        key: 'importDate',
                    },
                ]
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 申请禁用
            isShowDis: false,

            // 上传列表
            saveFileList: [],
            saveReFileList: [],
            fileTypeList: {
                bl_url: '营业执照',
                tca_url: '收款协议',
                gtc_url: '纳税人证明',
                b_r_rul: '开票记录',
            },
            fileNameList: ['营业执照', '收款协议', '纳税人证明', '开票记录'],
            uploadFilter: {
                list: [],
                time: ''
            },

            // 操作日志
            isOpeLogShow: false
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'createSupplierInfo'
        ]),
        addFormItemOption () {
            return this.addFormItemConfig[this.editData.paymentType];
        }
    },
    methods: {
        ...mapActions([
            'GetSupplierCreateListInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['supplier_create_list']);
            }

            let obj = {
                status: this.queryData.state,
                supplier_code: this.queryData.supplierCode.replace(/\s+/g, ','),
                type: this.queryData.supplierType,
                shipping_province: this.queryData.deliverAddress[0],
                shipping_city: this.queryData.deliverAddress[1],
                settle_method: this.queryData.settlementType,
                create_user: this.queryData.createUser,
                devlop_user: this.queryData.devUser,
                supplier_name: this.queryData.supplierName,
                contacts: this.queryData.liaisons,
                pay_method: this.queryData.paymentMode,
                start_time: this.queryData.createTime[0],
                end_time: this.queryData.createTime[1],
                ...params
            };
            console.log(obj, 'obj');

            this.GetSupplierCreateListInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                supplierCode: '',
                supplierName: '',
                supplierType: '',
                settlementType: '',
                devUser: '',
                deliverAddress: [],
                paymentMode: '',
                liaisons: '',
                createUser: '',
                createTime: [],
                state: '',
            };
        },

        //高级筛选
        handleClickFilter () {
            this.FilterShow = !this.FilterShow;
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 天眼查数据预览
        handlePreviewDetail ({credit_code}) {
            getSupplierDetailInfo({
                credit_code: credit_code
            }).then(({data}) => {
                this.queryPreviewData = data.result;
                this.isQueryPreview = true;
            });
        },

        // 保存上传文件
        handleSaveFileList (file, fileList) {
            clearTimeout(this.uploadFilter.time);
            this.uploadFilter.list.push(file);
            this.uploadFilter.time = setTimeout(() => {

                for (let i = 0; i < this.uploadFilter.list.length; i++) {
                    const fileName = this.uploadFilter.list[i].name.replace(/.[a-zA-Z]+$/, '');
                    if (this.fileNameList.indexOf(fileName) === -1) {
                        this.$refs.upload.clearFiles();
                        this.uploadFilter.list.splice(0);
                        this.$message.error('请确认文件名！');
                        return;
                    }
                }

                let newFileList = [];
                if (this.saveFileList.length) {
                    this.uploadFilter.list.forEach(value => {
                        const newFileName = value.name.replace(/.[a-zA-Z]+$/, '');
                        for (let i = 0; i < this.saveFileList.length; i++) {
                            const oldFileName = this.saveFileList[i].name.replace(/.[a-zA-Z]+$/, '');
                            if (oldFileName === newFileName) {
                                this.saveFileList.splice(i, 1, value);
                                break;
                            } else if (i === this.saveFileList.length - 1) {
                                newFileList.push(value);
                            }
                        }
                    });
                } else {
                    newFileList = [...this.uploadFilter.list];
                }

                this.saveFileList = [...this.saveFileList, ...newFileList];
                this.$refs.upload.clearFiles();
                this.uploadFilter.list.splice(0);
            }, 10);
        },

        // 选择发货地址
        handleSelectDeliver (val) {
            let obj = {
                province_code: val[0]
            };
            for (let item of this.optionList.deliverAddressList) {
                if (item.value === val[0] && item.children.length) return;
            }
            getCityInfo(obj).then(({data}) => {
                this.optionList.deliverAddressList.map(value => {
                    if (value.city_code === val[0]) {
                        value.children = [
                            ...data.data
                        ];
                    }
                });
            });
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 操作日志
        handleOperaiontLog ({supplier_id, user_id}) {
            this.saveCheckId = supplier_id;
            this.saveUserId = user_id;
            this.isOpeLogShow = true;
        },

        // 删除文件
        handleDeleteFile (inx) {
            this.saveFileList.splice(inx, 1);
            this.saveReFileList.splice(inx, 1);
        },

        // 单个创建
        handleSupplierCreate () {
            this.$store.commit('SAVE_OPTION_LIST_INFO', {
                key: 'supplier',
                value: this.optionList
            });
            goToNewPage(this, {
                pageMain: '/supplier_create_list',
                pageUrl: '/add_supplier_list',
                pageName: '单个创建',
                pageParams: 'supplier'
            }, '_new');
        },

        // 批量创建
        handleSupplierBatchCreate () {
            goToNewPage(this, {
                pageMain: '/supplier_create_list',
                pageUrl: '/batch_create_supplier',
                pageName: '批量创建'
            }, '_new');
        },

        // 点击编辑
        handleClickEdit ({supplier_id, status_code}) {
            if (status_code !== '4' && status_code !== '1') return;
            let obj = {
                supplier_id,
                status: status_code
            };
            getEditInfo(obj).then(({data}) => {

                // 加载地址
                this.handleSelectDeliver([data.data.data.province_code]);
                this.saveCheckId = supplier_id;
                this.isEditShow = true;
                this.editData.comCode = data.data.data.credit_code;
                this.editData.state = status_code;
                this.editData.supplierName = data.data.data.supplier_name;
                this.editData.supplierCode = data.data.data.supplier_code;
                this.editData.applyAddress = data.data.data.reg_address;
                this.editData.applyDate = data.data.data.reg_time;
                this.editData.companyType = data.data.data.type;
                this.editData.supplierLink = data.data.data.web_link;
                this.editData.pAndCity = [data.data.data.province_code, '1000'];
                this.editData.area = data.data.data.shipping_address;
                this.editData.contacts = data.data.data.contacts;
                this.editData.phoneNumber = data.data.data.mobile;
                this.editData.contactsPhone = data.data.data.phone;
                this.editData.wxNumber = data.data.data.wechat;
                this.editData.qqNumber = data.data.data.qq;
                this.editData.email = data.data.data.email;
                this.editData.paymentType = data.data.data.pay_method;
                this.editData.settlementType = data.data.data.settle_method;
                this.editData.point = data.data.data.tax_point + '%';
                this.editData.devUser = data.data.data.devlop_user;
                this.editData.auditUser = data.data.data.check_user;
                this.editData.importDate = data.data.data.create_time;
                this.editData.gatheringAccount = data.data.data.receive_account;
                this.editData.bankName = data.data.data.receive_bank;
                this.editData.gatheringName = data.data.data.receive_name;
                this.editData.gatheringId = data.data.data.receive_id_number;
                this.editData.gatheringPhone = data.data.data.receive_mobile;
                this.editData.shopName = data.data.data.shop_name;

                for (let key in this.fileTypeList) {
                    if (data.data.data[key]) {
                        imageUrlChangeFile(data.data.data[key], this.fileTypeList[key], file => {
                            this.saveReFileList.push(file);
                            this.saveFileList.push({
                                name: this.fileTypeList[key],
                                url: data.data.data[key]
                            });
                        });
                    }
                }
            });
        },

        // 关闭编辑
        handleCloseEdit () {
            for (let key in this.editData) {
                if (Array.isArray(this.editData[key])) {
                    this.editData[key] = [];
                } else {
                    this.editData[key] = '';
                }
            }
            this.$refs.editData.resetFields();
            this.saveFileList.splice(0);
            this.saveReFileList.splice(0);
            this.isEditShow = false;
        },

        // 保存编辑信息
        handleSaveEditInfo (type) {
            this.$refs.editData.validate(res => {
                if (res) {
                    if (!this.editData.wxNumber.replace(/\s/g, '') && !this.editData.qqNumber.replace(/\s/g, '')) {
                        this.$message.warning('请输入QQ号或微信号！');
                        return;
                    }
                    let obj = {
                        supplier_id: this.saveCheckId,
                        credit_code: this.editData.comCode,
                        supplier_code: this.editData.supplierCode,
                        supplier_name: this.editData.supplierName,
                        settle_method: this.editData.settlementType,
                        tax_point: this.editData.point.replace('%', ''),
                        type: this.editData.companyType,
                        devlop_user: this.editData.devUser,
                        status: this.editData.state,
                        // modify_time: this.editData.aaaa,
                        web_link: this.editData.supplierLink,
                        contacts: this.editData.contacts,
                        mobile: this.editData.phoneNumber,
                        phone: this.editData.contactsPhone,
                        wechat: this.editData.wxNumber,
                        qq: this.editData.qqNumber,
                        email: this.editData.email,
                        province_code: this.editData.pAndCity[0],
                        city_code: this.editData.pAndCity[1],
                        shipping_address: this.editData.area,
                        pay_method: this.editData.paymentType,
                        shop_name: this.editData.shopName,
                        receive_name: this.editData.gatheringName,
                        receive_account: this.editData.gatheringAccount,
                        receive_mobile: this.editData.gatheringPhone,
                        receive_id_number: this.editData.gatheringId,
                        receive_bank: this.editData.bankName,
                        submit_type: type,
                        reg_address: this.editData.applyAddress,
                        reg_time: this.editData.applyDate,
                        create_time: this.editData.importDate
                    };

                    // saveFileList

                    let formData = new FormData();
                    this.saveFileList.forEach((value, index) => {
                        if (value.raw) {
                            formData.append(`file_image_data[${index}]`, value.raw);
                        } else {
                            formData.append(`file_image_data[${index}]`, this.saveReFileList[index]);
                        }
                    });

                    for (let key in obj) {
                        formData.append(key, obj[key]);
                    }

                    saveEditInfo(formData).then(({data}) => {
                        this.handleOperationCallBack(data);
                    });
                }
            });
        },

        // 提交审核
        handleSubmitAudit () {
            if (!this.saveCheckInfo.length) return;
            this.$confirm('确认提交审核？', '提示', {
                type: 'warning'
            }).then(() => {

                let obj = comChangeSupplierState(2, '', this.saveCheckInfo);

                changeSupplierState(obj).then(({data}) => {
                    this.handleOperationCallBack(data);
                });
            });
        },

        // 申请禁用
        handleApplyDisabled () {
            if (!this.saveCheckInfo.length) return;
            this.isShowDis = true;
        },

        // 确认禁用
        handleConfirmDisabled ({remark}) {
            let obj = comChangeSupplierState(5, remark, this.saveCheckInfo);
            changeSupplierState(obj).then(({data}) => {
                this.handleOperationCallBack(data);
            });
        },

        // 上传附件
        handleUploadFile () {
            goToNewPage(this, {
                pageMain: '/supplier_create_list',
                pageUrl: '/create_supplier_upload',
                pageName: '上传附件'
            }, '_new');
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
                if (this.isEditShow) {
                    this.handleCloseEdit();
                }
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'supplier_create_list',
                value: JSON.stringify(this.queryData)
            });

            if (!this.optionList.deliverAddressList.length) {
                let deliverList = [];
                data.select_list.province_info.forEach(value => {
                    let obj = {
                        city_name: value.province_name,
                        city_code: value.province_code
                    };
                    deliverList.push({
                        ...obj,
                        children: []
                    });
                });

                this.optionList.deliverAddressList = [...deliverList];
                this.handleSelectDeliver(this.queryData.deliverAddress);
            }

            // 渲染下拉
            this.optionList.supplierTypeList = data.select_list.sup_type;
            this.optionList.settlementTypeList = data.select_list.sup_settle_method;

            this.optionList.devUserList = getArrListByObj(data.select_list.devlop_user);

            this.optionList.paymentModeList = data.select_list.sup_pay_method;

            this.optionList.createUserList = getArrListByObj(data.select_list.create_user);

            this.optionList.stateList = data.select_list.sup_status;
            this.optionList.companyTypeList = data.select_list.sup_type;
            this.optionList.pointList = getArrListByObj(data.select_list.make_tax_point);

            //添加开发者列表
            for (let i = 1; i < 4; i++) {
                this.addFormItemConfig[i].map((item) => {
                    if (item.userList) {
                        return item.userList = data.select_list.devlop_user;
                    }
                });
            }

            console.log(this.addFormItemConfig);

            //数据渲染
            this.tableData = data.data.value;
            this.tableKey = [...data.data.key];
            this.tableKey.push('操作');
            this.tableWidth = ['50px', '100px', '180px', '150px', '180px', '180px', '150px', '280px', '80px', '', '100px'];

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
            this.tableProps = ['index', 'supplier_code', 'supplier_name', 'shipping', 'contacts_info', 'payment_info', 'operate_dev_user', 'file_url', 'status', 'reject_remarks', 'operation'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.createSupplierInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['supplier_create_list']);
                this.handleQueryInfoCallBack(this.createSupplierInfo);

                if (this.queryParams) {
                    this.handleQueryInfo({limit: this.pageData.currentList});
                }
            }
        } catch (e) {
            console.log(e);
        }
    }
    ,
}
;
</script>

<style lang="less" scoped>
.ui-form--label_layout {
    /deep/ .el-form-item__label {
        line-height: 20px;
    }
}
</style>



