<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录" template="cascader"
                                           :options="optionList.productListList"
                                           :props="{ value: 'id', children: 'son_id', label: 'category_name' }"
                                           v-model="queryData.productList"></SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业组员" template="search" :options="optionList.workUserList"
                                           v-model="queryData.workUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="作业节点" template="search" :options="optionList.functionInfoList"
                                           v-model="queryData.functionInfo" @searchChange="getOperateUser">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核状态" :isDisabled="!queryData.functionInfo" template="search"
                                           :options="optionList.statusList"
                                           v-model="queryData.auditState">
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作用时">
                            <el-select :disabled="!queryData.functionInfo" size="small"
                                       v-model="queryData.operateUseType"
                                       @change="handleTimeRange(queryData.operateUseType)">
                                <el-option v-for="(label,value) in optionList.timeTypeList"
                                           :label="label"
                                           :value="value"
                                           :key="label">

                                </el-option>
                            </el-select>
                            <el-slider
                                :disabled="!queryData.functionInfo"
                                v-if="queryData.operateUseType"
                                v-model="queryData.operateUseTime"
                                range
                                :min="1"
                                :max="queryData.operateUseMax">
                            </el-slider>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员" :isDisabled="!queryData.functionInfo" template="search"
                                           :options="optionList.operateUserList"
                                           v-model="queryData.operateUser">
                        </SearchRequirement>
                    </li>
                    <transition name="slide-fade" @after-enter="animation()" @after-leave="animation()"
                                @before-enter="animation(1)">
                        <div v-show="FilterShow">
                            <li>
                                <SearchRequirement label="操作时间" template="daterange"
                                                   v-model="queryData.operateTime"></SearchRequirement>
                            </li>
                        </div>
                    </transition>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})"
                               icon="icon-chaxun">查询
                    </el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="!FilterShow">高级筛选<i class="el-icon-caret-bottom"></i></el-button>
                    <el-button size="small"
                               type="text"
                               @click="handleClickFilter"
                               v-show="FilterShow">基本筛选<i class="el-icon-caret-top"></i></el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon
                :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <!--<el-button-->
            <!--v-if="item.method && item.permit(thisVm)"-->
            <!--v-for="item in buttonList"-->
            <!--size="small"-->
            <!--:type="item.type"-->
            <!--:key="item.value"-->
            <!--@click="item.action(thisVm, item.method)">{{item.name}}</el-button>-->
            <!--<el-button @click="handleOperationFn(5)" type="text" size="small">-->
            <!--<i class="icon-shenhe icon-warn"></i>审核</el-button>-->
            <!--<el-button @click="handleOperationFn(4)" type="text" size="small">-->
            <!--<i class="icon-bohui icon-danger"></i>驳回</el-button>-->
        </div>
        <ComTableInfo
            ref="myTable"
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :tableWidth="tableWidth"
            :showSearch="showSearch"
            @checked="handleSaveCheckInfo">
            <template slot="spu" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.spu[0]}}</p>
                    <p>{{scope.row.spu[1]}}</p>
                    <div class="ui-table--supplier_code--flag" :class="`ui-table_flag${scope.row.devp_code}`"
                         v-show="scope.row.devp_type">
                        <span>{{scope.row.devp_type}}</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <i class="icon-bianji icon-main" @click.stop="handlePreview(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="img" slot-scope="scope">
                <LoadingImg :src="scope.row.photo_path" @click="handleLookImage(scope.row.photo_path)"></LoadingImg>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>
                    <p><span>{{scope.row.good_name[0]}}</span></p>
                    <p><span>{{scope.row.good_name[1]}}</span></p>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.category_path"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="sample_type" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.sample_type"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.good_info"><span>{{item}}</span></p>
                    <div class="ui-table--icon_btn">
                        <el-popover
                            placement="bottom"
                            title="采样链接"
                            width="200"
                            trigger="click">
                            <li v-for="item in scope.row.link_reference">
                                <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                            </li>
                            <el-button slot="reference" type="text"><i class="icon-caiyanglianjie icon-main"></i>
                            </el-button>
                        </el-popover>
                        <el-popover
                            placement="bottom"
                            title="参考链接"
                            width="200"
                            trigger="click">
                            <li v-for="item in scope.row.link_reference">
                                <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                            </li>
                            <el-button slot="reference" type="text"><i class="icon-cankaolianjie icon-main"></i>
                            </el-button>
                        </el-popover>
                    </div>
                </div>
            </template>
            <template slot="audit_state" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.product_staus"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="ope_record" slot-scope="scope">
                <div v-for="(item, index) in scope.row.opera_log" v-if="scope.row.opera_log.length<=3" :key="index">
                    {{item}}
                </div>
                <el-popover v-if="scope.row.opera_log.length>3"
                            style="cursor: pointer;"
                            placement="left"
                            trigger="hover">
                    <div v-for="(item, index) in scope.row.opera_log"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.opera_log" v-if="index<3" :key="index">{{item}}
                        </div>
                        <div v-if="scope.row.opera_log.length>3">...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="operate" slot-scope="scope">
                <el-table-column
                    width="200px"
                    fixed="right"
                    label="操作">
                    <template slot-scope="scope">
                        <el-button type="text" size="small" @click="handleOperationFn(scope.row, 1)">审核</el-button>
                        <el-button type="text" size="small" @click="handleOperationFn(scope.row, 2)">驳回</el-button>
                    </template>
                </el-table-column>
            </template>

            <template slot="use_time" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.opera_time"><span>{{item}}</span></p>
                </div>
            </template>

        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <EditPage :show.sync="isPreviewShow" :row="saveEditRow" type="preview"></EditPage>
        <ComDialogRemarks showRemarks formLabel="选择部门"
                          title="审核" type="checkbox"
                          :visible.sync="isShowAudit"
                          :checkList="auditDptList"
                          @confirm="handleConfirmOperate" :option="optionList.depList"></ComDialogRemarks>
        <ComDialogRemarks showRemarks title="驳回" :visible.sync="isShowReject"
                          @confirm="handleConfirmOperate"></ComDialogRemarks>
    </div>
</template>

<script>
import EditPage from './ProductDatumCreate/EditPage';
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage,
    getNumByTime,
    getArrListByObj
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';
import {
    getSupplierCreateListInfo,
    changeSupplierState,
    getEditInfo
} from '@/api/dev_department/supplier_development/supplier_create_list';

import {
    SPUOperationInfo
} from '@/api/dev_department/product_development/product_datum_create';

import { getAuditDptBySpu } from '@/api/dev_department/product_development/dev_anomaly_audit';
import { getUserByfunction } from '@/api/common';

export default {
    components: {
        EditPage
    },
    data () {
        return {

            //高级筛选
            FilterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
            },

            optionList: {
                supplierTypeList: {},
                settlementTypeList: {},
                devUserList: {},
                deliverAddressList: [],
                paymentModeList: {},
                liaisonsList: {},
                createUserList: {},
                stateList: {},
                companyTypeList: {},
                // depList: {
                //     // '1': '开发',
                //     '2': '品控',
                //     '3': '文案',
                //     '4': '摄影',
                //     '5': '美工',
                // },
                depList: {},
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
                workUserList: [],
                operateUserList: [],
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 预览
            saveEditRow: {},
            isPreviewShow: false,

            // 审核驳回
            isShowAudit: false,
            auditDptList: [],
            isShowReject: false,
            saveCheckStatus: {
                spu: '',
                status: ''
            }
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'devAnomalyAuditInfo'
        ])
    },
    methods: {
        ...mapActions([
            'GetDevAnomalyAuditInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.functionInfo}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['dev_anomaly_audit']);
            }

            let obj = {
                spu: this.queryData.productSPU,
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join(','),
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function: this.queryData.functionInfo,
                user: this.queryData.workUser,
                opera_user: this.queryData.operateUser,
                opera_time_start: this.queryData.operateTime[0],
                opera_time_end: this.queryData.operateTime[1],
                status: this.queryData.auditState,
                opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
                ...params
            };

            this.GetDevAnomalyAuditInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
            };
        },

        //高级筛选
        handleClickFilter () {
            this.FilterShow = !this.FilterShow;
        },

        // 操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.operateUseMax = 30;
                    break;
                case 'hours':
                    this.queryData.operateUseMax = 24;
                    break;
                case 'minutes':
                    this.queryData.operateUseMax = 60;
                    break;
            }
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 预览
        handlePreview (row) {
            this.isPreviewShow = true;
            this.saveEditRow = row;
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 重构树形结构
        handleTreeCallBackChange (arr) {
            for (let i = 0; i < arr.length; i++) {
                if (arr[i].son_id && arr[i].son_id.length) {
                    this.handleTreeCallBackChange(arr[i].son_id);
                } else {
                    delete arr[i].son_id;
                }
            }
        },

        // 页面操作功能
        handleOperationFn (row, status) {
            this.saveCheckStatus.spu = row.spu[0];
            this.saveCheckStatus.status = status;
            if (status === 1) {
                if (row.audit_status !== '1') {
                    this.$confirm('是否审核?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        this.handleConfirmOperate();
                    });
                } else {
                    getAuditDptBySpu({spu: row.spu[0]}).then(({data}) => {
                        if (data.status) {
                            this.auditDptList = data.data.flow;

                            this.optionList.depList = data.data.flow_list;
                        }

                        this.isShowAudit = true;

                    });

                }
            } else {
                this.isShowReject = true;
            }
            // if (row) {
            //
            // } else {
            //     if (!this.saveCheckInfo.length) {
            //         this.$message.warning('请勾选数据！');
            //         return;
            //     }
            //
            //     let spus = [];
            //     this.saveCheckInfo.forEach(value => {
            //         spus.push(value.spu[0])
            //     });
            //
            //     SPUOperationInfo({
            //         spu: spus,
            //         status: status
            //     }).then(({ data }) => {
            //         this.handleOperationCallBack(data);
            //     })
            // }
        },

        // 审核 驳回
        handleConfirmOperate (res = {}) {
            let obj = {
                spu: this.saveCheckStatus.spu,
                type: this.saveCheckStatus.status,
                status: 4
            };

            if (obj.type === 1 && res.checkList) {
                obj.dep = res.checkList.join(',');
            }

            SPUOperationInfo(obj).then(({data}) => {
                this.handleOperationCallBack(data);
            });
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'dev_anomaly_audit',
                value: JSON.stringify(this.queryData)
            });

            //数据渲染
            this.tableData = data.data.value;
            this.tableData.map((item) => {
                return item.opera_log.reverse();
            });
            this.tableKey = data.data.key;
            // this.tableWidth[0] = '50px';
            // this.tableWidth[2] = '80px';
            // this.tableWidth[6] = '230px';
            // this.tableWidth[8] = '230px';
            this.tableWidth = ['50', '100', '80', '250', '150', '100', '250', '100', '200', '120', '180'];

            if (data.select_list) {
                this.optionList.productListList = [...data.select_list.category];
                this.optionList.functionInfoList = getArrListByObj(data.select_list.function, true);
                this.optionList.statusList = getArrListByObj(data.select_list.status, true);
                this.handleTreeCallBackChange(this.optionList.productListList);
                this.optionList.workUserList = getArrListByObj(data.select_list.opera_user, true);
            }
            this.tableProps = ['index', 'spu', 'img', 'good_name', 'category_path', 'sample_type', 'good_info', 'audit_state', 'ope_record', 'use_time', 'remarks'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.devAnomalyAuditInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['dev_anomaly_audit']);
                this.handleQueryInfoCallBack(this.devAnomalyAuditInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>

.ui-table--supplier_code {
    font-size: 12px;
    line-height: 10px;

    p:nth-child(1) {
        /*font-weight: bold;*/
    }

    p {
        margin-bottom: 8px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    background: #63c4e9;
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
    font-weight: normal;
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: 185px;
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 185px;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

.ui-table__img img {
    max-width: 70px;
    height: 80px;
}
</style>



