            <template slot="img" slot-scope="scope">
                <LoadingImg :src="scope.row.photo_path" @click="handleLookListImage(scope.row)"></LoadingImg>
            </template>
            <template slot="good_name" slot-scope="scope">
                <div>
                    <p><span>{{scope.row.good_name[0]}}</span></p>
                    <p><span>{{scope.row.good_name[1]}}</span></p>
                </div>
            </template>
            <template slot="category_path" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.category_path"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="sample_type" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.sample_type"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="good_info" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.good_info"><span>{{item}}</span></p>
                    <div class="ui-table--icon_btn">
                        <el-popover
                            placement="bottom"
                            title="采样链接"
                            width="200"
                            trigger="click">
                            <ul>
                                <li v-for="item in scope.row.link">
                                    <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                                </li>
                            </ul>
                            <el-button slot="reference" type="text"><i class="icon-caiyanglianjie"></i></el-button>
                        </el-popover>
                        <el-popover
                            placement="bottom"
                            title="参考链接"
                            width="200"
                            trigger="click">
                            <li v-for="item in scope.row.link_reference">
                                <a :href="item" target="_blank" class="ui-link">{{item}}</a>
                            </li>
                            <el-button slot="reference" type="text"><i class="icon-cankaolianjie"></i></el-button>
                        </el-popover>
                    </div>
                </div>
            </template>
            <template slot="audit_state" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.product_staus"><span>{{item}}</span></p>
                </div>
            </template>
            <template slot="ope_record" slot-scope="scope">
                <div v-for="(item, index) in scope.row.opera_log" v-if="scope.row.opera_log.length<=3" :key="index">
                    {{item}}
                </div>
                <el-popover v-if="scope.row.opera_log.length>3"
                            style="cursor: pointer;"
                            placement="left"
                            trigger="hover">
                    <div v-for="(item, index) in scope.row.opera_log"
                         :key="index">{{item}}
                    </div>
                    <div slot="reference">
                        <div v-for="(item, index) in scope.row.opera_log" v-if="index<3" :key="index">{{item}}</div>
                        <div v-if="scope.row.opera_log.length>3">...</div>
                    </div>
                </el-popover>
            </template>
            <template slot="use_time" slot-scope="scope">
                <div>
                    <p v-for="item of scope.row.opera_time"><span>{{item}}</span></p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        <EditPage :show.sync="isPreviewShow" :row="saveEditRow" type="preview"></EditPage>
        <ComDialogRemarks showRemarks formLabel="选择部门"
                          title="审核" type="select"
                          :visible.sync="isShowAudit"
                          @confirm="handleConfirmOperate" :option="optionList.depList"></ComDialogRemarks>
        <ComDialogRemarks showRemarks title="驳回" :visible.sync="isShowReject"
                          @confirm="handleConfirmOperate"></ComDialogRemarks>
        <el-dialog title="生成二维码" class="ui-layout_edit-dialog" width="1000px" top="10vh" :visible.sync="isPrintPreview"
                   @close="handleCancelPrint">
            <div class="ui-qrcode">
                <div class="ui-qrcode--area">
                    <div class="ui-qrcode--item" v-for="(skuArr, spu, inx) in printShowList">
                        <div class="ui-qrcode--item--title">
                            <span>SPU:{{spu}}</span>
                            <el-checkbox :indeterminate="isIndeterminate[inx]" v-model="printCheckAll[inx]"
                                         @change="handleCheckAllChange(inx, spu, printCheckAll[inx])">全选
                            </el-checkbox>
                        </div>
                        <ul class="clearFix">
                            <li v-for="(item, index) in skuArr">
                                <span class="el-icon-circle-check"
                                      :class="{'ui-qrcode--check': printCheckList[inx] && printCheckList[inx].indexOf(index) > -1}"></span>
                                <img :src="item.url" alt="" @click="handleClickQRCode(inx, index, spu)">
                                <span>{{item.sku}}</span>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div slot="footer">
                <el-button size="small" @click="handleCancelPrint">取消打印</el-button>
                <el-button type="primary" size="small" @click="handleQRCodePrint">打印</el-button>
            </div>
        </el-dialog>
        <LookImage :src="previewImgUrl" :show.sync="isShowPreviewImg"></LookImage>
        <PrintQRCode ref="qrcode" :data="printData"></PrintQRCode>
        <PreviewComImage :imgs="previewListImage" :show.sync="isShowListImg"></PreviewComImage>
        <PreviewImage :show.sync="isShowArtImg" :row="previewArtInfo"></PreviewImage>
    </div>
</template>

<script>
import EditPage from '../../DevDepartment/ProductDevelopment/ProductDatumCreate/EditPage';
import PreviewImage from '../../DesignDepartment/ArtistManage/PreviewImage';
import PreviewComImage from '../../../../components/PreviewComImage';
import PrintQRCode from './PrintQRCode';
import config from '@/assets/js/config';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage,
    getNumByTime,
    getArrListByObj,
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';

import {
    createQRCodeInfo
} from '@/api/sample_track_manage/product_manage_module/product_dev_list';

import {
    previewImageDetailInfo,
} from '@/api/design_department/artist_manage/art_history_img';

import { getUserByfunction } from '@/api/common';

export default {
    components: {
        EditPage,
        PrintQRCode,
        PreviewComImage,
        PreviewImage
    },
    data () {
        return {

            //高级筛选
            FilterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
            },

            optionList: {
                functionInfoList: {},
                operateUserList: {},
                statusList: {},
                workUserList: {},
                supplierTypeList: {},
                settlementTypeList: {},
                devUserList: {},
                deliverAddressList: [],
                paymentModeList: {},
                liaisonsList: {},
                createUserList: {},
                stateList: {},
                companyTypeList: {},
                depList: {
                    '1': '品控',
                    '2': '文案or摄影',
                    '3': '美工'
                },
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 预览
            saveEditRow: {},
            isPreviewShow: false,

            // 审核驳回
            isShowAudit: false,
            isShowReject: false,
            saveCheckStatus: {
                spu: '',
                status: ''
            },

            // 生成二维码
            isPrintPreview: false,
            printData: '',

            // 勾选打印
            isIndeterminate: [],
            printCheckList: [],
            printCheckAll: [],
            printShowList: {},

            // 预览图片
            previewImgUrl: '',
            isShowPreviewImg: false,

            // 预览列表图片
            isShowListImg: false,
            previewListImage: [],
            isShowArtImg: false,
            previewArtInfo: {},
        };
    },
    computed: {
        ...mapGetters([
            'buttonList',
            'queryParamsInfo',
            'productDevListInfo'
        ])
    },
    methods: {
        ...mapActions([
            'GetProductDevListInfo'
        ]),
        autoAddTableInx,
        getDefaultTime,
        goToNewPage,

        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.functionInfo}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {

            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['com_dev_list']);
            }

            let obj = {
                spu: this.queryData.productSPU,
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join(','),
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function: this.queryData.functionInfo,
                user: this.queryData.workUser,
                opera_user: this.queryData.operateUser,
                opera_time_start: this.queryData.operateTime[0],
                opera_time_end: this.queryData.operateTime[1],
                status: this.queryData.auditState,
                opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
                ...params
            };

            this.GetProductDevListInfo(obj).then(({data}) => {

                // 导出
                if (data.status === 2) return;

                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
            };
        },

        //高级筛选
        handleClickFilter () {
            this.FilterShow = !this.FilterShow;
        },

        // 预览图片
        handlePreviewImage (src) {
            this.previewImgUrl = src;
            this.isShowPreviewImg = true;
        },

        // 操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.operateUseMax = 30;
                    break;
                case 'hours':
                    this.queryData.operateUseMax = 24;
                    break;
                case 'minutes':
                    this.queryData.operateUseMax = 60;
                    break;
            }
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 预览
        handlePreview (row) {
            this.isPreviewShow = true;
            this.saveEditRow = row;
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        // 预览列表图片
        handleLookListImage (row) {
            if (row.is_art_image == '1' && row.art_type) {
                let spu = row.spu[0];
                if (row.art_type.split(',').length > 1) {
                    let types = row.art_type.split(',');
                    let listInfo1 = [];
                    previewImageDetailInfo({
                        image_type: types[0],
                        spu: spu
                    }).then(({data}) => {
                        listInfo1 = data.data.list;
                        previewImageDetailInfo({
                            image_type: types[1],
                            spu: spu
                        }).then(({data}) => {
                            let listInfo2 = data.data.list;
                            if (!Array.isArray(listInfo1)) {
                                for (let [key, value] of Object.entries(listInfo1.spu)) {
                                    let newVal = {
                                        '1': value
                                    };
                                    if (!Array.isArray(listInfo2)) {
                                        newVal['2'] = data.data.list.spu[key];
                                    }
                                    listInfo1.spu[key] = newVal;
                                }
                                for (let [key, value] of Object.entries(listInfo1.sku)) {
                                    let newVal = {
                                        '1': value
                                    };
                                    if (!Array.isArray(listInfo2)) {
                                        if (listInfo2.sku[key]) {
                                            newVal['2'] = listInfo2.sku[key];
                                        } else {
                                            newVal['2'] = {
                                                main_pics: [],
                                                assiant_pics: []
                                            };
                                        }
                                    }
                                    listInfo1.sku[key] = newVal;
                                }
                            } else if (!Array.isArray(listInfo2)) {
                                for (let [key, value] of Object.entries(listInfo2.spu)) {
                                    let newVal = {
                                        '2': value
                                    };
                                    listInfo1.spu[key] = newVal;
                                }
                                for (let [key, value] of Object.entries(listInfo2.sku)) {
                                    let newVal = {
                                        '2': value
                                    };
                                    listInfo1.sku[key] = newVal;
                                }
                            } else {
                                return;
                            }
                            return;
                            this.isShowArtImg = true;
                            this.previewArtInfo = {
                                checks: 1,
                                ...listInfo1,
                                imgTypeList: {
                                    '1': '实拍图',
                                    '2': '实拍无logo',
                                    '3': '试卖',
                                    '4': '代销'
                                },
                                imgType: row.art_type
                            };
                        });
                    });
                } else {
                    previewImageDetailInfo({
                        image_type: row.art_type,
                        spu: spu
                    }).then(({data}) => {
                        console.log(data);
                        return;
                        this.isShowArtImg = true;
                        this.previewArtInfo = {
                            ...data.data.list,
                            imgTypeList: {
                                '1': '实拍图',
                                '2': '实拍无logo',
                                '3': '试卖',
                                '4': '代销'
                            },
                            imgType: row.art_type
                        };
                    });
                }
            } else if (Array.isArray(row.is_art_image)) {
                this.isShowListImg = true;
                this.previewListImage = row.is_art_image;
            }
        },

        // 重构树形结构
        handleTreeCallBackChange (arr) {
            for (let i = 0; i < arr.length; i++) {
                if (arr[i].son_id && arr[i].son_id.length) {
                    this.handleTreeCallBackChange(arr[i].son_id);
                } else {
                    delete arr[i].son_id;
                }
            }
        },

        // 页面操作功能
        handleOperationFn (row, status) {
            this.saveCheckStatus.spu = row.spu[0];
            this.saveCheckStatus.status = status;
            if (status === 1) {
                this.isShowAudit = true;
            } else {
                this.isShowReject = true;
            }
            // if (row) {
            //
            // } else {
            //     if (!this.saveCheckInfo.length) {
            //         this.$message.warning('请勾选数据！');
            //         return;
            //     }
            //
            //     let spus = [];
            //     this.saveCheckInfo.forEach(value => {
            //         spus.push(value.spu[0])
            //     });
            //
            //     SPUOperationInfo({
            //         spu: spus,
            //         status: status
            //     }).then(({ data }) => {
            //         this.handleOperationCallBack(data);
            //     })
            // }
        },

        // 审核 驳回
        handleConfirmOperate (res) {
            let obj = {
                spu: this.saveCheckStatus.spu,
                type: this.saveCheckStatus.status,
                status: 4
            };

            if (obj.type === 1) {
                obj.dep = res.check;
            }

            SPUOperationInfo(obj).then(({data}) => {
                this.handleOperationCallBack(data);
            });
        },

        // 生成二维码
        handleCreateQRCode () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }

            let skuList = '';
            this.saveCheckInfo.forEach(value => {
                // skuList.push(value)
                skuList += value.sku + ',';
            });

            createQRCodeInfo({
                sku_list: skuList.replace(/,$/, '')
            }).then(({data}) => {
                this.isPrintPreview = true;
                this.printShowList = data.data.list;
            });
        },

        // 全选打印
        handleCheckAllChange (inx, spu, check) {
            this.isIndeterminate[inx] = false;
            if (check) {
                this.printCheckList[inx] = [];
                this.printShowList[spu].forEach((value, index) => {
                    this.printCheckList[inx].push(index);
                });
            } else {
                this.printCheckList[inx].splice(0);
            }
        },

        // 单选
        handleClickQRCode (inx, index, spu) {
            if (!Array.isArray(this.printCheckList[inx])) {
                this.$set(this.printCheckList, inx, []);
                this.printCheckList[inx].push(index);
            } else {
                let _has = this.printCheckList[inx].indexOf(index);
                if (_has > -1) {
                    this.printCheckList[inx].splice(_has, 1);
                } else {
                    this.printCheckList[inx].push(index);
                }
            }
            this.isIndeterminate[inx] = this.printCheckList[inx] > 0 && this.printCheckList[inx] < this.printShowList[spu].length;
            this.$set(this.isIndeterminate, inx, this.printCheckList[inx].length > 0 && this.printCheckList[inx].length < this.printShowList[spu].length);
            this.$set(this.printCheckAll, inx, this.printCheckList[inx].length === this.printShowList[spu].length);
        },

        handleQRCodePrint () {

            let obj = [];
            let keys = Object.keys(this.printShowList);
            this.printCheckList.forEach((value, index) => {
                if (value && value.length) {
                    // this.
                    value.forEach(inx => {
                        obj.push(this.printShowList[keys[index]][inx].sku);
                    });
                }
            });

            this.printData = obj.join(',');

            setTimeout(() => {
                this.$refs.qrcode.handlePrint();
            }, 100);
        },

        // 取消打印
        handleCancelPrint () {
            this.isPrintPreview = false;
            this.printData = '';
            this.isIndeterminate.splice(0);
            this.printCheckList.splice(0);
            this.printCheckAll.splice(0);
            this.printShowList = {};
        },

        // 操作回调
        handleOperationCallBack (data) {
            if (data.status) {
                this.$message.success('操作成功');
                this.handleQueryInfo({limit: this.pageData.currentList});
            } else {
                this.$message.error(data.errorMess || '操作失败');
            }
        },

        //查询回调
        handleQueryInfoCallBack (data) {

            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'com_dev_list',
                value: JSON.stringify(this.queryData)
            });

            //数据渲染
            this.tableData = data.data.value;
            this.tableData.map((item) => {
                return item.opera_log.reverse();
            });

            this.tableKey = data.data.key;
            this.tableWidth[2] = '80px';
            this.tableWidth[6] = '230px';
            this.tableWidth[8] = '230px';
            this.optionList.productListList = [...data.select_list.category];
            if (data.select_list.user) {
                this.optionList.workUserList = getArrListByObj(data.select_list.user, true);
            }
            this.optionList.functionInfoList = getArrListByObj(data.select_list.function, true);
            this.optionList.statusList = getArrListByObj(data.select_list.status, true);
            this.handleTreeCallBackChange(this.optionList.productListList);

            // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
            this.tableProps = ['index', 'spu', 'img', 'good_name', 'category_path', 'sample_type', 'good_info', 'audit_state', 'ope_record', 'use_time', 'remarks'];

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        }
    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.productDevListInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['com_dev_list']);
                this.handleQueryInfoCallBack(this.productDevListInfo);
            }
        } catch (e) {
            console.log(e);
        }
    },
};
</script>

<style lang="less" scoped>

.ui-table--supplier_code {
    font-size: 12px;
    line-height: 10px;

    p:nth-child(1) {
        /*font-weight: bold;*/
    }

    p {
        margin-bottom: 8px;
    }
}

/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

.ui-table--supplier_code--flag {
    width: 50px;
    height: 35px;
    line-height: 54px;
    text-align: center;
    -webkit-transform: rotateZ(45deg);
    transform: rotateZ(45deg);
    position: absolute;
    font-size: 12px;
    color: #FFFFFF;
    top: -12px;
    right: -20px;
    font-weight: normal;
}

.ui-table--icon_btn {
    i {
        cursor: pointer;
        color: #72afff;
        font-size: 14px;
    }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
    li {
        width: 130px;
        /*height: 185px;*/
        margin-right: 20px;
        text-align: center;
        position: relative;

        img {
            width: 100%;
            height: 185px;
        }

        .ui-com_edit__upload--item--mask {

            display: none;
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 185px;
            background: rgba(0, 0, 0, .3);
            text-align: center;
            line-height: 185px;

            & > div {
                display: inline-block;
            }

            span {
                margin: 0 5px;
                cursor: pointer;
                color: #cccccc;
                font-size: 20px;

                &:hover {
                    color: #FFFFFF;
                }
            }
        }

        &:hover {
            .ui-com_edit__upload--item--mask {
                display: block;
            }
        }
    }
}

.ui-qrcode {
    text-align: left;

    .ui-qrcode--title {
        height: 40px;
        background: #f3f3f3;
        font-size: 14px;
        padding-left: 20px;
        line-height: 40px;
    }

    .ui-qrcode--area {

    }

    .ui-qrcode--item {
        padding-top: 10px;

        .ui-qrcode--item--title {
            height: 40px;
            line-height: 40px;
            font-size: 14px;
            color: #6798ff;
            padding: 0 10px;
            background: #f3f3f3;
            display: flex;
            justify-content: space-between;

        }

        .el-icon-circle-check {
            color: transparent;
            border-radius: 50%;
            position: absolute;
            top: 4px;
            right: 4px;
            font-size: 18px;
        }

        .ui-qrcode--check {
            position: absolute;
            top: 4px;
            right: 4px;
            font-size: 18px;
            color: #6798ff;
            background: #FFFFFF;
            border-radius: 50%;
        }

        ul {
            li {
                position: relative;
                float: left;
                padding: 10px;
                width: 100px;
                font-size: 14px;
                text-align: center;

                img {
                    width: 100%;
                }
            }
        }
    }
}

.el-icon-warning {
    color: #F56C6C;
}

.el-icon-success {
    color: #67C23A;
}

.ui-table__img img {
    max-width: 70px;
    max-height: 90px;
}
</style>



