<template>
    <div class="ui-scan_page ui-main-module">
        <div class="ui-head">
            <ul class="ui-head-ope_item">
                <li class="ui-head-ope_item--layout">
                    <el-input size="small" autofocus @keyup.enter.native="handleScanInfo" placeholder="请扫描二维码"
                              v-model="queryData.code">
                    </el-input>
                </li>
            </ul>
        </div>

        <div class="ui-fn-module">
            <el-button type="text" size="small" @click="handleDelScan">
                <i class="icon-shanchusaoma icon_pink"></i>删除扫码
            </el-button>
            <el-button type="text" size="small" @click="handleSendScan">
                <i class="icon-querendaoyang icon_blue"></i>提交送样
            </el-button>
        </div>
        <ComTableInfo
            isCheck
            :tableKey="tableKey"
            :tableProps="tableProps"
            :tableData="tableData"
            :other-height="['ui-fn-module','ui-head']"
            @checked="handleSaveCheckInfo"
            :tableWidth="tableWidth">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in optionList.moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.list_type === item.value && 'ui-active'">{{item.label}}</span>
                    </li>
                </el-badge>
            </div>
            <template slot="create_user" slot-scope="scope">
                <p>开发部：{{scope.row.create_user[0]}}</p>
                <p>品控部：{{scope.row.create_user[1]}}</p>
            </template>

            <template slot="create_time" slot-scope="scope">
                <p>开发部：{{scope.row.create_time[0]}}</p>
                <p>品控部：{{scope.row.create_time[1]}}</p>
            </template>

        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="pageData.currentPage"
                :page-sizes="pageSizes"
                :page-size="pageData.currentList"
                layout="total, sizes, prev, pager, next, jumper"
                :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <el-dialog title="查看台车明细" class="ui-layout_edit-dialog" :visible.sync="dialogTableVisible">
            <ComTableInfo
                :tableKey="detailKey"
                :tableProps="detailProps"
                :tableData="detailData"
                :tableWidth="detailWidth">
                <template slot="index" slot-scope="scope">
                    <p>{{autoAddTableInx(scope.$index)}}</p>
                </template>
                <template slot="skus" slot-scope="scope">
                    <p v-for="(item,index) in scope.row.skus.split(',')" :key="index">{{item}}</p>
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>
import config from '@/assets/js/config';
import { mapActions, mapGetters } from 'vuex';
import { makeLog, isValidData } from '@/services';
import {
    autoAddTableInx,
    getDefaultTime,
    changeTableInfoProps,
    goToNewPage
} from '@/services/index';
import {
    getCityInfo
} from '@/api/login';
import {
    // 查询
    getShotScanInfo,
    // 提交送样
    submitShotCodes,
    getDetail
} from '@/api/design_department/photography_manage/photography_sample_scan';

export default {
    data () {
        return {
            thisVm: {},

            queryData: {
                code: '',
                user_id: '',
                user_name: '',
                list_type: '1',
                department: '',
            },

            optionList: {
                moduleTypeList: [
                    {
                        label: '商品台车',
                        value: '1'
                    },
                    {
                        label: '商品SPU',
                        value: '3'
                    }
                ]
            },

            tableData: [],
            tableKey: ['编号', '商品台车', '商品箱号', '商品目录', '商品数量', '送样人', '送样时间', '备注说明'],
            tableWidth: [],
            tableProps: [],

            //查看台车明细
            dialogTableVisible: false,
            detailData: [],
            detailKey: [],
            detailWidth: [],
            detailProps: [],

            // 保存勾选数据
            saveCheckInfo: [],
            // 保存列表数据
            cacheList: {
                trolleyData: [],
                spuData: [],
            },

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

        };
    },
    computed: {
        ...mapGetters(['shotScanListInfo', 'queryParamsInfo']),
    },
    methods: {
        ...mapActions(['SaveShotScanListInfo']),

        //显示明细
        showDetail (row) {
            if (!row) {
                return;
            }

            this.dialogTableVisible = true;
            let obj = {
                trolley_id: row.trolley_id,
                box_id: row.box_id,
                bind_type: 3
            };
            getDetail(obj).then(({data}) => {
                this.detailData = data.data;
                this.detailProps = Object.keys(data.data[0]);
                this.detailProps.unshift('index');
                this.detailKey = ['编号', '商品SPU', '商品名称', '商品目录', '商品数量', '关联SKU', '备注说明'];
            });
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            switch (this.queryData.list_type) {
                case '1' :
                    this.handleQueryInfoCallBack(this.cacheList.trolleyData);
                    break;
                case '3' :
                    this.handleQueryInfoCallBack(this.cacheList.spuData);
            }
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.list_type = val;
            this.handleQueryInfo();
        },

        //通过工号查询用户信息
        handleUserInfo () {

            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'shot_scan',
                value: JSON.stringify(this.queryData)
            });

        },

        // 扫码送样
        handleScanInfo () {
            const handle_type = parseInt(this.queryData.list_type, 10);
            const code = this.queryData.code;
            getShotScanInfo({handle_type, code}).then(({data}) => {
                if (data.status !== 1) {
                    this.$message({
                        message: data.errMsg,
                        type: 'warning'
                    });
                    return;
                }
                this.queryData.code = '';

                // 渲染扫码列表
                this.handleQueryInfoCallBack(data.list);
            });
        },

        //查询回调&&缓存list数据
        handleQueryInfoCallBack (data) {
            const type = parseInt(this.queryData.list_type, 10);

            if (isValidData(data)) {
                //数据渲染
                const tabData = [...data];

                for (let i in tabData) {
                    let n = parseInt(i, 10) + 1;
                    tabData[i] = Object.assign({num: n}, tabData[i]);
                }

                this.tableData = tabData;

                this.tableProps = Object.keys(tabData[0]);

            } else {
                this.tableData = [];
            }

            this.tableWidth[0] = '90px';
            //添加缓存&渲染列表
            switch (type) {
                case 1 : // 台车
                    this.tableWidth[4] = '90px';
                    this.tableWidth[5] = '200px';
                    this.tableWidth[7] = '300px';
                    this.tableKey = ['编号', '商品台车', '商品箱号', '商品目录', '商品数量', '送样人', '送样时间', '备注说明'];
                    this.cacheList.trolleyData = data;
                    break;
                case 3 : // SPU
                    this.tableWidth[1] = '90px';
                    this.tableWidth[2] = '200px';
                    this.tableKey = ['编号', '商品SPU', '商品名称', '样品数量', '送样人', '送样时间', '备注说明'];
                    this.cacheList.spuData = data;
                    break;
                default:
                    break;
            }

            // 保存扫码列表
            this.SaveShotScanListInfo(this.cacheList);
        },

        //删除扫码
        handleDelScan (checkList = []) {
            if (!this.saveCheckInfo.length && checkList.length === 0) return this.$message.warning('请勾选数据！');

            checkList = checkList.length > 0 ? checkList : this.saveCheckInfo;
            let restArr = [...this.tableData];
            checkList.forEach(check => {
                const num = check.num;
                restArr = restArr.filter(item => item.num !== num);
            });
            this.handleQueryInfoCallBack(restArr);
        },

        //提交送样
        handleSendScan () {
            if (!this.saveCheckInfo.length) return this.$message.warning('请勾选数据！');
            // this.queryData.list_type 操作类型 1、台车&箱子   3、spu

            const saveCheckInfoBackup = [...this.saveCheckInfo];
            this.$confirm('确认提交？', '提示', {type: 'warning'})
                .then(() => {
                    const handle_type = parseInt(this.queryData.list_type, 10);
                    const submit_data = this.getSumbitData(this.saveCheckInfo);
                    submitShotCodes({handle_type, submit_data})
                        .then(({data}) => {
                            if (data.status === 1) {
                                // 删除提交的项目
                                this.handleDelScan(saveCheckInfoBackup);
                                return this.$message.success(data.errorMess || '提交成功！');
                            }
                            return this.$message.warning(data.errorMess || '提交失败！');
                        });
                });
        },

        // 获取选中数据的submit数据
        getSumbitData (dataList) {
            const type = parseInt(this.queryData.list_type, 10);
            // 获取对象数组中对应key值对象的辅助函数
            const formataData = (data, keys) => {
                keys = Array.isArray(keys) ? [...keys] : keys.split(',');
                return data.map(item => ({
                    ...keys.reduce((pre, cur) => ({
                        ...pre,
                        [cur]: item[cur]
                    }), {})
                }));
            };

            switch (type) {
                case 1: // 台车
                    return formataData(dataList, 'trolley_id,box_id');
                case 3: // spu
                    return formataData(dataList, 'spu');
                default:
                    return null;
            }
        },
    },
    created () {
        if (isValidData(this.shotScanListInfo)) {
            this.cacheList = this.shotScanListInfo;
            this.handleQueryInfo();
        } else {

            let obj = {
                handle_type: this.queryData.list_type,
                is_frist: 1,
                code: ''
            };

            getShotScanInfo(obj);

        }
        this.tableWidth[0] = '90px';
        this.tableWidth[4] = '90px';

        if (this.queryParamsInfo['shot_scan']) {
            this.queryData = JSON.parse(this.queryParamsInfo['shot_scan']);
        }
    }
};
</script>

<style scoped lang="less">
@border-color: #e6e6e6;
.ui-scan_page {
    text-align: left;

    .ui-head {
        padding: 20px;
        background: #FFFFFF;
        border: 1px solid @border-color;
    }

    .ui-head-ope_item {
        /deep/ .el-input {
            width: 200px;
        }

        li:not(:last-child) {
            margin-bottom: 10px;
        }

        .ui-head-ope_item--layout {
            .el-input {
                width: 609px;
            }
        }
    }
}
</style>




