<script>
import config from '@/assets/js/config';
import {
    getNumByTime,
    getArrListByObj
} from '@/services/index';
import {
    mapActions,
    mapGetters
} from 'vuex';
import {
    getCityInfo
} from '@/api/login';
import {
    getPhotographyGoodsListInfo,
    getPhotographyWorkerList,
    assignTask,
    getRejectInfo,
    photographyGoodsReject,
    submitPhotographyGoods
} from '@/api/design_department/photography_manage/photography_goods_list';
import { upLoadPhoto, getUserByfunction } from '@/api/common';

export default {
    data () {
        return {

            //高级筛选
            filterShow: false,

            // 时间搜索框宽度设置
            searchDateWidth: config.SEARCH_DATE_WIDTH,

            thisVm: {},

            // 搜索缩进
            showSearch: true,

            queryData: {
                moduleType: '1',
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
            },

            optionList: {
                moduleTypeList: [
                    {
                        label: '待分配',
                        value: '1'
                    },
                    {
                        label: '待拍摄',
                        value: '2'
                    },
                    {
                        label: '拍摄记录',
                        value: '3'
                    }
                ],

                productListList: [],
                functionInfoList: {},
                statusList: {},
                timeTypeList: {
                    day: '天',
                    hours: '时',
                    minutes: '分'
                },
                operateUserList: [],
                workUserList: [],
            },

            tableData: [],
            tableKey: [],
            tableWidth: [],
            tableProps: [],

            // 保存勾选数据
            saveCheckInfo: [],

            //分页
            pageSizes: config.pageData.pageSizes,
            pageData: {
                currentPage: 1,
                currentList: 20,
                currentTotal: 0,
            },

            // 预览图片
            isShowLookImage: false,
            lookImageUrl: [],

            // 上传列表
            saveFileList: [],

            //文案小组和商品分类
            classSearch: [
                {
                    title: '全部',
                    number: 400
                },
                {
                    title: '手机周边',
                    number: 35
                },
                {
                    title: '全部',
                    number: 350
                },
                {
                    title: '服饰珠宝',
                    number: 50000
                },
                {
                    title: '服饰珠宝',
                    number: 5
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 50121212
                },
                {
                    title: '服饰珠宝',
                    number: 5012121
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501212
                },
                {
                    title: '服饰珠宝',
                    number: 501213213
                },
            ],

            //指派任务弹窗
            isShowAssign: false,

            //驳回
            isShowReject: false,
            rejectData: [],
            rejectList: [],
            rejectDefault: {},

            //是否是管理员
            isLeader: false,

        };
    },
    computed: {
        ...mapGetters([
            'queryParamsInfo',
            'photographyGoodsListInfo'
        ]),

    },
    methods: {
        ...mapActions([
            'GetPhotographyGoodsListInfo'
        ]),

        // 操作用时
        handleTimeRange (type) {

            switch (type) {
                case 'day':
                    this.queryData.operateUseMax = 30;
                    break;
                case 'hours':
                    this.queryData.operateUseMax = 24;
                    break;
                case 'minutes':
                    this.queryData.operateUseMax = 60;
                    break;
            }
        },

        //切换作业节点 获取相应员工列表
        getOperateUser () {
            getUserByfunction({function: this.queryData.functionInfo}).then(({data}) => {
                if (data.status) {
                    this.optionList.operateUserList = getArrListByObj(data.data.user_operate);
                }
            });
        },
        //字符串 转换 数组
        getArrByStr (str) {
            let arr = str + '';
            arr = arr.split(',');
            return arr;
        },
        //搜索功能
        querySearch (queryString, cb) {
            let restaurants = this.optionList.groupUser;
            restaurants.forEach((item) => {
                //没有value值 会有问题
                item.value = item.user_name;
            });

            let results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter (queryString) {
            return (restaurant) => {
                return (restaurant.user_name.toLowerCase().indexOf(queryString.toLowerCase()) >= 0);
            };
        },

        // 列表信息查询
        handleQueryInfo (params = {}, isFn) {
            // 判断是否为功能参数
            if (isFn) {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['photography_goods_list']);
            }

            let obj = {
                list_type: this.queryData.moduleType,
                spu: this.queryData.productSPU.split(' ').join(','),
                supplier_code: this.queryData.supplierCode,
                spu_category: this.queryData.productList.join('->'),
                goods_name: this.queryData.productName,
                supplier_name: this.queryData.supplierName,
                function: this.queryData.functionInfo,
                user: this.queryData.workUser,
                opera_user: this.queryData.operateUser,
                opera_time_start: this.queryData.operateTime[0],
                opera_time_end: this.queryData.operateTime[1],
                status: this.queryData.auditState,
                opera_start_time: getNumByTime(this.queryData.operateUseTime[0], this.queryData.operateUseType, 'seconds'),
                opera_end_time: getNumByTime(this.queryData.operateUseTime[1], this.queryData.operateUseType, 'seconds'),
                ...params
            };
            this.GetPhotographyGoodsListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        // 查询重置
        handleResetQueryInfo () {
            this.queryData = {
                moduleType: '1',
                productSPU: '',
                supplierCode: '',
                productList: [],
                workUser: '',
                functionInfo: '',
                auditState: '',
                productName: '',
                supplierName: '',
                operateUser: '',
                operateTime: [],
                operateUseTime: [1, 20],
                operateUseType: '',
                operateUseMax: 30,
            };
        },

        //高级筛选
        handleClickFilter () {
            this.filterShow = !this.filterShow;
        },

        animation (isHide) {
            if (isHide) {
                console.log(document.querySelector('body'));
                document.querySelector('body').style.overflow = 'hidden';
                return;
            }
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 分页条数切换
        handleSizeChange (val) {
            this.handleQueryInfo({
                limit: val,
                offset: 1
            }, true);
        },

        // 跳转页数切换
        handleCurrentChange (val) {
            this.handleQueryInfo({
                limit: this.pageData.currentList,
                offset: val
            }, true);
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //勾选的id
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj.spu[0]);
            });
            return idArr;
        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.moduleType = val;
            this.handleQueryInfo();
            this.$refs.myTable.handleRefreshTableHeight();
        },

        // 导出明细信息
        handleExportListInfo () {
            this.handleQueryInfo({
                isExcel: 1
            }, true);
        },

        // 保存上传文件
        handleSaveFileList (file, row) {

            let formData = new FormData();
            formData.append('file', file.raw);

            upLoadPhoto(formData).then(({data}) => {
                if (data.status == 1) {
                    row.image = data.data.path;
                } else {
                    this.$message.error(data.errorMess);
                }
            });
            console.log(row.image, 8899);
        },

        // 预览图片
        handleLookImage (src) {
            this.isShowLookImage = true;
            this.lookImageUrl = src;
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            // 保存查询参数
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'photography_goods_list',
                value: JSON.stringify(this.queryData)
            });

            //数据渲染

            this.isLeader = data.is_leader;
            this.tableData = data.data.value;
            this.tableWidth = ['60', '150', '80', '200', '150', '80', '250', '120', '220', '120', ''];
            this.tableData.map((item) => {
                return item.opera_log.reverse();
            });
            this.tableProps = Object.keys(data.data.key);
            this.tableKey = Object.values(data.data.key);

            if (data.select_list) {
                this.optionList.productListList = [...data.select_list.category];
                this.optionList.functionInfoList = getArrListByObj(data.select_list.function, true);
                this.optionList.statusList = getArrListByObj(data.select_list.status, true);
                if (data.select_list.user) {
                    this.optionList.workUserList = getArrListByObj(data.select_list.user);
                }
            }

            // if (data.select_list.group_user) {
            //     data.select_list.group_user.forEach((item) => {
            //         this.optionList.groupUser[item.user_code] = item.user_name;
            //     });
            // }

            //渲染分页
            this.pageData.currentList = data.page.limit * 1;
            this.pageData.currentPage = data.page.offset * 1;
            this.pageData.currentTotal = data.page.total * 1;
        },

        //是否勾选
        checkedStatus (obj) {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this[obj] = true;
        },

        //指派任务
        handleClickAssign () {
            this.checkedStatus('isShowAssign');
        },

        //确认指派
        handleConfirmAssign (val) {
            console.log(val.check, 88);
            let obj = {
                spu: this.selectId().join(','),
                distr_user: val.check
            };
            assignTask(obj).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo();
                } else {
                    this.$message.error('操作失败');
                }
            });
        },

        // 驳回
        handleClickReject () {
            this.checkedStatus('isShowReject');
        },

        //驳回操作数据处理
        rejectTypeChange (row) {
            let list = row.rejectList;
            let data = list.find((item) => {
                return row.type === item.id;
            });
            row.setting_introduce = data.setting_introduce;
            row.remark = data.remark;

            row.reject_to_dpt = data.reject_to_dpt;
            row.reject_to_dpts = data.reject_to_dpts.split('/');

        },

        //获取驳回原因
        handleRejectInfo () {

            getRejectInfo({reject_from_dpt: 4}).then(({data}) => {
                this.rejectList = data.data.list;
                this.rejectDefault = {
                    rejectList: this.rejectList,
                    type: '',
                    setting_introduce: '',
                    reject_to_dpt: '',
                    reject_to_dpts: [],
                    remark: '',
                    image: ''
                };
                this.rejectData.push(Object.assign({}, this.rejectDefault));
            });
        },

        // 确认驳回
        handleConfirmReject () {
            let arr = [];
            this.rejectData.forEach((item) => {
                arr.push({
                    reject_reason: item.type,
                    reject_content: item.setting_introduce,
                    reject_image: item.image,
                    type: item.reject_to_dpt
                });
            });

            let obj = {
                spu: this.selectId().join(','),
                reject_data: arr
            };

            console.log(obj, 99);

            photographyGoodsReject(obj).then(({data}) => {
                if (data.status === 1) {
                    this.$message.success('操作成功!');
                    this.isShowReject = false;
                    this.handleQueryInfo();
                    this.rejectData.length = 0;
                    this.rejectData.push(Object.assign({}, this.rejectDefault));
                    console.log(data, 123);
                } else {
                    this.$message.error('操作失败! 请检查填写是否正确!');
                }
            });

        },

        // 删除驳回行
        handleDeleteInput (idx) {
            this.rejectData.splice(idx, 1);
        },

        //添加一行
        handleAddInput (idx) {

            this.rejectData.splice(idx + 1, 0, Object.assign({}, this.rejectDefault));
        },

        // 提交
        handleClickSubmit () {
            if (!this.saveCheckInfo.length) {
                this.$message.warning('请勾选数据！');
                return;
            }
            this.$confirm('确认提交？', '提示', {
                type: 'warning'
            }).then(() => {
                submitPhotographyGoods({spu: this.selectId().join(',')}).then(({data}) => {
                    if (data.status === 1) {

                        this.$message.success('操作成功!');
                    } else {

                        this.$message.error('操作失败!');
                    }
                });
            });
        },

        //小组分类
        searchGroup (value) {
            console.log(value);
        },

    },
    created () {
        this.thisVm = this;
        try {
            if (JSON.stringify(this.photographyGoodsListInfo) === '{}') {
                this.handleQueryInfo({is_get_base: 1});
            } else {
                // 获取保存的查询参数
                this.queryData = JSON.parse(this.queryParamsInfo['photography_goods_list']);
                this.handleQueryInfoCallBack(this.photographyGoodsListInfo);
            }
        } catch (e) {
            console.log(e);
        }

        getPhotographyWorkerList().then(({data}) => {
            let obj = {};
            if (data.data) {
                data.data.forEach((item) => {
                    obj[item.user_number] = item.user_name;
                });
                this.optionList.workerList = obj;
            }
        });

        this.handleRejectInfo();

    }
};
</script>

<style lang="less" scoped>
/deep/ .el-table td, /deep/ .el-table th {
    overflow: hidden;
    text-align: left !important;
}

/deep/ .el-input, /deep/ .el-select {
    width: 100%;
}

.ui-ope_area--btn {
    cursor: pointer;
}

</style>



